/* PhotoModal Styles */
.photo-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 20px;
  animation: fadeIn 0.3s ease-out;
}

.photo-modal {
  background: white;
  border-radius: 20px;
  max-width: 90vw;
  max-height: 90vh;
  overflow: hidden;
  position: relative;
  animation: slideIn 0.3s ease-out;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.5);
  will-change: transform;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
}

.photo-modal-close {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 40px;
  height: 40px;
  border: none;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  font-weight: bold;
  color: #333;
  z-index: 10;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.photo-modal-close:hover {
  background: white;
  transform: scale(1.1);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
}

.photo-modal-content {
  display: flex;
  flex-direction: column;
  max-height: 90vh;
}

.photo-modal-image-container {
  flex: 1;
  min-height: 300px;
  max-height: 60vh;
  overflow: hidden;
  position: relative;
  background: #f8f9fa;
  display: flex;
  align-items: center;
  justify-content: center;
}

.photo-modal-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  display: block;
  image-rendering: -webkit-optimize-contrast;
  image-rendering: crisp-edges;
  image-rendering: pixelated;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
}

.photo-modal-details {
  padding: 30px;
  background: white;
  flex-shrink: 0;
}

.photo-modal-name {
  font-size: 1.8rem;
  font-weight: 700;
  color: #1e3a8a;
  margin-bottom: 8px;
}

.photo-modal-role {
  font-size: 1.1rem;
  color: #3b82f6;
  font-weight: 600;
  margin-bottom: 15px;
}

.photo-modal-bio {
  font-size: 1rem;
  color: #6b7280;
  line-height: 1.6;
  margin-bottom: 20px;
}

.photo-modal-contact {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.photo-modal-phone {
  color: #3b82f6;
  text-decoration: none;
  font-weight: 600;
  font-size: 1rem;
  padding: 12px 16px;
  border-radius: 10px;
  background: rgba(59, 130, 246, 0.1);
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  max-width: fit-content;
}

.photo-modal-phone:hover {
  background: rgba(59, 130, 246, 0.2);
  transform: translateX(5px);
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: scale(0.9) translateY(20px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .photo-modal-overlay {
    padding: 10px;
  }
  
  .photo-modal {
    max-width: 95vw;
    max-height: 95vh;
    border-radius: 15px;
  }
  
  .photo-modal-close {
    top: 10px;
    right: 10px;
    width: 35px;
    height: 35px;
    font-size: 20px;
  }
  
  .photo-modal-image-container {
    min-height: 250px;
    max-height: 50vh;
  }
  
  .photo-modal-details {
    padding: 20px;
  }
  
  .photo-modal-name {
    font-size: 1.5rem;
  }
  
  .photo-modal-role {
    font-size: 1rem;
  }
  
  .photo-modal-bio {
    font-size: 0.95rem;
  }
  
  .photo-modal-phone {
    font-size: 0.95rem;
    padding: 10px 14px;
  }
}

@media (max-width: 480px) {
  .photo-modal-overlay {
    padding: 5px;
  }
  
  .photo-modal {
    max-width: 98vw;
    max-height: 98vh;
    border-radius: 10px;
  }
  
  .photo-modal-close {
    top: 8px;
    right: 8px;
    width: 30px;
    height: 30px;
    font-size: 18px;
  }
  
  .photo-modal-image-container {
    min-height: 200px;
    max-height: 45vh;
  }
  
  .photo-modal-details {
    padding: 15px;
  }
  
  .photo-modal-name {
    font-size: 1.3rem;
  }
  
  .photo-modal-role {
    font-size: 0.95rem;
  }
  
  .photo-modal-bio {
    font-size: 0.9rem;
  }
  
  .photo-modal-phone {
    font-size: 0.9rem;
    padding: 8px 12px;
  }
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .photo-modal-close {
    background: white;
    border: 2px solid #000;
    color: #000;
  }
  
  .photo-modal-phone {
    border: 2px solid #3b82f6;
  }
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
  .photo-modal-overlay,
  .photo-modal,
  .photo-modal-close,
  .photo-modal-phone {
    animation: none;
    transition: none;
  }
  
  .photo-modal-close:hover,
  .photo-modal-phone:hover {
    transform: none;
  }
} 