/* AboutPage Styles */
.about-page {
  min-height: 100vh;
}

/* Hero Section - Enhanced for better visibility and UX */
.hero-section {
  position: relative;
  min-height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
  overflow: hidden;
  background: linear-gradient(135deg, #0f4c75 0%, #3282b8 25%, #bbe1fa 50%, #3282b8 75%, #0f4c75 100%);
  background-size: 400% 400%;
  animation: enhancedGradientShift 20s ease infinite;
}

.hero-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 80%, rgba(255, 255, 255, 0.15) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(255, 255, 255, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 40% 40%, rgba(255, 255, 255, 0.08) 0%, transparent 50%);
  animation: floatingLights 15s ease-in-out infinite;
  z-index: 1;
}

.hero-section::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="hero-pattern" width="100" height="100" patternUnits="userSpaceOnUse"><circle cx="20" cy="20" r="1.5" fill="%23ffffff" opacity="0.2"/><circle cx="80" cy="80" r="1.5" fill="%23ffffff" opacity="0.2"/><circle cx="50" cy="10" r="1" fill="%23ffffff" opacity="0.25"/><circle cx="10" cy="60" r="1" fill="%23ffffff" opacity="0.25"/><circle cx="90" cy="40" r="1" fill="%23ffffff" opacity="0.25"/><circle cx="30" cy="90" r="2" fill="%23ffffff" opacity="0.15"/><circle cx="70" cy="30" r="2" fill="%23ffffff" opacity="0.15"/></pattern></defs><rect width="100" height="100" fill="url(%23hero-pattern)"/></svg>');
  opacity: 0.6;
  animation: patternFloat 25s ease-in-out infinite;
  z-index: 1;
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, #0f4c75 0%, #3282b8 25%, #bbe1fa 50%, #3282b8 75%, #0f4c75 100%);
  background-size: 400% 400%;
  animation: enhancedGradientShift 20s ease infinite;
}

.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, 
    rgba(15, 76, 117, 0.3) 0%, 
    rgba(50, 130, 184, 0.2) 25%, 
    rgba(187, 225, 250, 0.1) 50%, 
    rgba(50, 130, 184, 0.2) 75%, 
    rgba(15, 76, 117, 0.3) 100%);
  z-index: 2;
}

.hero-section .container {
  position: relative;
  z-index: 3;
  max-width: 900px;
  margin: 0 auto;
  padding: 0 20px;
}

.hero-section h1 {
  font-size: clamp(3rem, 7vw, 5rem);
  font-weight: 900;
  margin-bottom: 25px;
  text-shadow: 
    0 4px 20px rgba(0, 0, 0, 0.4),
    0 2px 8px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(255, 255, 255, 0.1);
  animation: enhancedFadeInUp 1.2s cubic-bezier(0.4, 0, 0.2, 1) forwards;
  letter-spacing: -1px;
  line-height: 1.1;
  background: linear-gradient(135deg, #ffffff 0%, #f0f8ff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3));
}

.hero-subtitle {
  font-size: clamp(1.3rem, 3.5vw, 1.8rem);
  opacity: 1;
  line-height: 1.6;
  text-shadow: 
    0 3px 15px rgba(0, 0, 0, 0.4),
    0 1px 5px rgba(0, 0, 0, 0.3);
  animation: enhancedFadeInUp 1.2s cubic-bezier(0.4, 0, 0.2, 1) 0.4s both;
  font-weight: 500;
  max-width: 700px;
  margin: 0 auto;
  color: #f8f9ff;
  position: relative;
}

.hero-subtitle::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background: linear-gradient(90deg, transparent, #FFD700, transparent);
  border-radius: 2px;
  animation: shimmer 2s ease-in-out infinite;
}

@keyframes enhancedGradientShift {
  0%, 100% { 
    background-position: 0% 50%; 
    filter: hue-rotate(0deg);
  }
  25% { 
    background-position: 100% 50%; 
    filter: hue-rotate(5deg);
  }
  50% { 
    background-position: 100% 100%; 
    filter: hue-rotate(10deg);
  }
  75% { 
    background-position: 0% 100%; 
    filter: hue-rotate(5deg);
  }
}

@keyframes floatingLights {
  0%, 100% { 
    transform: translateY(0px) rotate(0deg);
    opacity: 0.8;
  }
  33% { 
    transform: translateY(-20px) rotate(1deg);
    opacity: 1;
  }
  66% { 
    transform: translateY(10px) rotate(-1deg);
    opacity: 0.9;
  }
}

@keyframes patternFloat {
  0%, 100% { 
    transform: translateY(0px) rotate(0deg) scale(1);
  }
  50% { 
    transform: translateY(-15px) rotate(0.5deg) scale(1.02);
  }
}

@keyframes enhancedFadeInUp {
  from {
    opacity: 0;
    transform: translateY(50px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

@keyframes shimmer {
  0%, 100% { 
    opacity: 0.5;
    transform: translateX(-50%) scaleX(0.8);
  }
  50% { 
    opacity: 1;
    transform: translateX(-50%) scaleX(1.2);
  }
}

/* Section Headers */
.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
  flex-wrap: wrap;
  gap: 20px;
}

.section-header h2 {
  font-size: clamp(2rem, 4vw, 2.5rem);
  font-weight: 700;
  color: #1e3a8a;
  margin: 0;
}

.section-description {
  font-size: 1.2rem;
  color: #64748b;
  margin: 10px 0 0 0;
  flex-basis: 100%;
}

/* Expand Button */
.expand-button {
  display: flex;
  align-items: center;
  gap: 8px;
  background: linear-gradient(135deg, #3b82f6 0%, #1e3a8a 100%);
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 25px;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(59, 130, 246, 0.3);
  position: relative;
  overflow: hidden;
}

.expand-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.6s ease;
}

.expand-button:hover::before {
  left: 100%;
}

.expand-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(59, 130, 246, 0.4);
  background: linear-gradient(135deg, #2563eb 0%, #1e40af 100%);
}

.expand-button.expanded {
  background: linear-gradient(135deg, #059669 0%, #047857 100%);
}

.expand-button.expanded:hover {
  background: linear-gradient(135deg, #047857 0%, #065f46 100%);
}

.expand-icon {
  transition: transform 0.3s ease;
  font-size: 0.8rem;
}

.expand-button.expanded .expand-icon {
  transform: rotate(180deg);
}

/* Story Section */
.story-section {
  padding: 80px 0;
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
  position: relative;
}

.story-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="story-pattern" width="100" height="100" patternUnits="userSpaceOnUse"><circle cx="20" cy="20" r="1" fill="%233b82f6" opacity="0.1"/><circle cx="80" cy="80" r="1" fill="%233b82f6" opacity="0.1"/><circle cx="50" cy="10" r="0.5" fill="%233b82f6" opacity="0.15"/><circle cx="10" cy="60" r="0.5" fill="%233b82f6" opacity="0.15"/><circle cx="90" cy="40" r="0.5" fill="%233b82f6" opacity="0.15"/></pattern></defs><rect width="100" height="100" fill="url(%23story-pattern)"/></svg>');
  opacity: 0.3;
}

.story-section .container {
  position: relative;
  z-index: 2;
}

.story-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
  align-items: start;
  max-height: 400px;
  overflow: hidden;
  transition: all 0.8s ease;
}

.story-content.expanded {
  max-height: 1200px;
  align-items: stretch;
}

.story-text {
  display: flex;
  flex-direction: column;
  gap: 25px;
  height: 100%;
}

.story-preview {
  font-size: 1.4rem;
  color: #1e3a8a;
  line-height: 1.7;
  font-weight: 600;
  margin-bottom: 10px;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
}

.story-details {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.8s ease;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.story-content.expanded .story-details {
  opacity: 1;
  transform: translateY(0);
}

.story-details p {
  font-size: 1.1rem;
  color: #4b5563;
  line-height: 1.7;
  margin-bottom: 0;
  padding: 15px;
  background: rgba(255, 255, 255, 0.7);
  border-radius: 10px;
  border-left: 4px solid #3b82f6;
  backdrop-filter: blur(5px);
  transition: all 0.3s ease;
}

.story-details p:hover {
  background: rgba(255, 255, 255, 0.9);
  transform: translateX(5px);
}

.story-image {
  position: relative;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  transition: all 0.8s ease;
  height: 350px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 3px solid rgba(59, 130, 246, 0.1);
  background: white;
}

.story-content.expanded .story-image {
  height: 500px;
}

.story-image:hover {
  transform: translateY(-5px);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.15);
}

.story-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  display: block;
  transition: all 0.8s ease;
  transform: scale(1.02);
}

.story-image:hover img {
  transform: scale(1.05);
}

.story-content.expanded .story-image img {
  transform: scale(1.01);
}

.story-content.expanded .story-image:hover img {
  transform: scale(1.03);
}

.story-image-placeholder {
  display: none;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #3b82f6 0%, #1e3a8a 100%);
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border-radius: 20px;
  color: white;
  text-align: center;
}

.story-image-placeholder span {
  font-size: 4rem;
  margin-bottom: 15px;
  display: block;
}

.story-image-placeholder p {
  font-size: 1.2rem;
  font-weight: 600;
  margin: 0;
}

/* Mission Vision Section - Always Visible */
.mission-vision-section {
  padding: 80px 0;
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
}

.mission-vision-section h2 {
  font-size: clamp(2rem, 4vw, 2.5rem);
  font-weight: 700;
  color: #1e3a8a;
  text-align: center;
  margin-bottom: 50px;
}

.mission-vision-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 40px;
  max-width: 1200px;
  margin: 0 auto;
}

.mission-card,
.vision-card {
  background: white;
  padding: 40px;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  border: 2px solid transparent;
  text-align: center;
}

.mission-card:hover,
.vision-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
  border-color: #3b82f6;
}

.mission-card h3,
.vision-card h3 {
  font-size: 1.8rem;
  font-weight: 700;
  color: #1e3a8a;
  margin-bottom: 20px;
}

.mission-card p,
.vision-card p {
  font-size: 1.1rem;
  color: #4b5563;
  line-height: 1.7;
}

/* Values Section - Always Visible */
.values-section {
  padding: 80px 0;
  background: white;
}

.values-section h2 {
  font-size: clamp(2rem, 4vw, 2.5rem);
  font-weight: 700;
  color: #1e3a8a;
  text-align: center;
  margin-bottom: 50px;
}

.values-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
  max-width: 1200px;
  margin: 0 auto;
}

.value-card {
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
  padding: 30px;
  border-radius: 15px;
  text-align: center;
  transition: all 0.3s ease;
  border: 2px solid transparent;
}

.value-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.1);
  border-color: #3b82f6;
}

.value-icon {
  font-size: 3rem;
  margin-bottom: 20px;
  display: block;
}

.value-card h3 {
  font-size: 1.4rem;
  font-weight: 700;
  color: #1e3a8a;
  margin-bottom: 15px;
}

.value-card p {
  font-size: 1rem;
  color: #6b7280;
  line-height: 1.6;
}

/* Team Section */
.team-section {
  padding: 80px 0;
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
}

.team-container {
  position: relative;
  overflow: hidden;
}

.team-container::before,
.team-container::after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  width: 40px;
  z-index: 10;
  pointer-events: none;
  transition: opacity 0.3s ease;
}

.team-container::before {
  left: 0;
  background: linear-gradient(90deg, rgba(248, 250, 252, 0.9) 0%, transparent 100%);
}

.team-container::after {
  right: 0;
  background: linear-gradient(270deg, rgba(248, 250, 252, 0.9) 0%, transparent 100%);
}

.team-container.expanded::before,
.team-container.expanded::after {
  opacity: 0;
}

/* Scroll indicator */
.team-scroll-indicator {
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 4px;
  background: rgba(59, 130, 246, 0.3);
  border-radius: 2px;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.team-container:not(.expanded) .team-scroll-indicator {
  opacity: 1;
}

.team-scroll-indicator::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 20px;
  background: #3b82f6;
  border-radius: 2px;
  animation: scrollIndicator 2s ease-in-out infinite;
}

@keyframes scrollIndicator {
  0%, 100% { transform: translateX(0); }
  50% { transform: translateX(40px); }
}

.team-scroll-wrapper {
  overflow-x: auto;
  overflow-y: hidden;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE and Edge */
  /* Enhanced touch scrolling */
  touch-action: pan-x;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  /* Prevent text selection during scroll */
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: transparent;
  /* Smooth momentum scrolling */
  scroll-snap-type: x mandatory;
}

.team-scroll-wrapper::-webkit-scrollbar {
  display: none; /* Chrome, Safari, Opera */
}

.team-grid {
  display: flex;
  gap: 30px;
  padding: 20px 0;
  min-width: max-content;
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.team-grid.expanded {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  min-width: auto;
  max-height: none;
  overflow: visible;
}

.team-member {
  flex: 0 0 300px;
  background: white;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: 2px solid transparent;
  transform: translateX(0);
  /* Enhanced hover effects for horizontal scroll */
  will-change: transform, opacity;
  opacity: 1;
  /* Scroll snap alignment */
  scroll-snap-align: start;
}

.team-member:hover {
  transform: translateY(-10px) scale(1.02);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
  border-color: #3b82f6;
}

.team-grid.expanded .team-member {
  flex: none;
  transform: translateX(0);
}



.member-image {
  position: relative;
  height: 250px;
  overflow: hidden;
  background: linear-gradient(135deg, #3b82f6 0%, #1e3a8a 100%);
  transition: all 0.3s ease;
}

.member-image:hover {
  transform: scale(1.02);
}

.member-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center top;
  transition: transform 0.3s ease;
  image-rendering: -webkit-optimize-contrast;
  image-rendering: crisp-edges;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
}

/* Special styling for Julius Ndukuthyo's photo to show the face in the middle */
.julius-photo {
  object-position: center !important;
}

.member-image-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.member-image:hover .member-image-overlay {
  opacity: 1;
}

.click-to-view {
  color: white;
  font-weight: 600;
  font-size: 0.9rem;
  background: rgba(0, 0, 0, 0.7);
  padding: 8px 16px;
  border-radius: 20px;
  backdrop-filter: blur(5px);
}

.member-placeholder {
  display: none;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #3b82f6 0%, #1e3a8a 100%);
}

.member-initials {
  font-size: 3rem;
  font-weight: 700;
  color: white;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.3);
}

.team-member:hover .member-image img {
  transform: scale(1.05);
}

.member-info {
  padding: 25px;
}

.member-info h3 {
  font-size: 1.4rem;
  font-weight: 700;
  color: #1e3a8a;
  margin-bottom: 8px;
}

.member-role {
  font-size: 1rem;
  color: #3b82f6;
  font-weight: 600;
  margin-bottom: 15px;
}

.member-bio {
  font-size: 0.95rem;
  color: #6b7280;
  line-height: 1.6;
  margin-bottom: 15px;
}

.member-contact {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.contact-link {
  color: #3b82f6;
  text-decoration: none;
  font-weight: 600;
  font-size: 0.9rem;
  padding: 8px 12px;
  border-radius: 8px;
  background: rgba(59, 130, 246, 0.1);
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  gap: 5px;
}

.contact-link:hover {
  background: rgba(59, 130, 246, 0.2);
  transform: translateX(5px);
}

/* Partnerships Section - Simplified */
.partnerships-section {
  padding: 80px 0;
  background: white;
}

.partnerships-section h2 {
  font-size: clamp(2rem, 4vw, 2.5rem);
  font-weight: 700;
  color: #1e3a8a;
  text-align: center;
  margin-bottom: 50px;
}

.partnerships-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  max-width: 1200px;
  margin: 0 auto;
}

.partnership-item {
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
  padding: 30px;
  border-radius: 15px;
  text-align: center;
  transition: all 0.3s ease;
  border: 2px solid transparent;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.partnership-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.1);
  border-color: #3b82f6;
}

.partnership-item h3 {
  font-size: 1.3rem;
  font-weight: 700;
  color: #1e3a8a;
  margin-bottom: 15px;
}

.partnership-item p {
  font-size: 1rem;
  color: #6b7280;
  line-height: 1.6;
}

/* Responsive Design */
@media (max-width: 768px) {
  .hero-section {
    min-height: 50vh;
  }
  
  .section-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
  }
  
  .expand-button {
    align-self: stretch;
    justify-content: center;
  }
  
  .story-content {
    grid-template-columns: 1fr;
    gap: 40px;
    max-height: 500px;
    align-items: center;
  }
  
  .story-content.expanded {
    max-height: 1500px;
    align-items: stretch;
  }
  
  .story-image {
    height: 300px;
  }
  
  .story-content.expanded .story-image {
    height: 400px;
  }
  
  .story-text {
    gap: 20px;
  }
  
  .story-preview {
    font-size: 1.2rem;
  }
  
  .story-details p {
    font-size: 1rem;
  }
  
  .mission-vision-grid {
    grid-template-columns: 1fr;
    gap: 30px;
  }
  
  .values-grid {
    grid-template-columns: 1fr;
    gap: 25px;
  }
  
  .team-container {
    margin: 0 -20px;
  }
  
  .team-scroll-wrapper {
    padding: 0 20px;
  }
  
  .team-grid {
    gap: 20px;
    padding: 15px 0;
  }
  
  .team-member {
    flex: 0 0 280px;
  }
  
  .team-grid.expanded {
    display: grid;
    grid-template-columns: 1fr;
    gap: 30px;
    padding: 0;
    max-height: none;
  }
  
  .team-grid.expanded .team-member {
    flex: none;
  }
  
  .partnerships-grid {
    grid-template-columns: 1fr;
    gap: 25px;
  }
  
  .member-image {
    height: 280px;
  }
  
  .member-image img {
    object-position: center top;
  }
  
  .julius-photo {
    object-position: center !important;
  }
  
  .member-image-overlay {
    opacity: 0.8;
  }
  
  .click-to-view {
    font-size: 0.8rem;
    padding: 6px 12px;
  }
  
  .mission-card,
  .vision-card {
    padding: 30px 20px;
  }
  
  .value-card {
    padding: 25px 20px;
  }
  
  .member-info {
    padding: 20px;
  }
  
  .partnership-item {
    padding: 25px 20px;
  }
}

@media (max-width: 480px) {
  .hero-section {
    min-height: 40vh;
  }
  
  .hero-section h1 {
    font-size: 2rem;
  }
  
  .hero-subtitle {
    font-size: 1.1rem;
  }
  
  .section-header h2 {
    font-size: 1.8rem;
  }
  
  .expand-button {
    padding: 10px 20px;
    font-size: 0.9rem;
  }
  
  .story-content {
    gap: 30px;
    max-height: 450px;
  }
  
  .story-content.expanded {
    max-height: 1200px;
  }
  
  .story-image {
    height: 250px;
  }
  
  .story-content.expanded .story-image {
    height: 350px;
  }
  
  .story-preview {
    font-size: 1.1rem;
  }
  
  .story-details p {
    font-size: 0.95rem;
  }
  
  .mission-card h3,
  .vision-card h3 {
    font-size: 1.5rem;
  }
  
  .mission-card p,
  .vision-card p {
    font-size: 1rem;
  }
  
  .value-card h3 {
    font-size: 1.2rem;
  }
  
  .team-container {
    margin: 0 -15px;
  }
  
  .team-scroll-wrapper {
    padding: 0 15px;
  }
  
  .team-grid {
    gap: 15px;
    padding: 10px 0;
  }
  
  .team-member {
    flex: 0 0 260px;
  }
  
  .team-grid.expanded {
    gap: 25px;
  }
  
  .member-info h3 {
    font-size: 1.2rem;
  }
  
  .member-image {
    height: 250px;
  }
  
  .member-image img {
    object-position: center top;
  }
  
  .julius-photo {
    object-position: center !important;
  }
  
  .member-image-overlay {
    opacity: 0.9;
  }
  
  .click-to-view {
    font-size: 0.75rem;
    padding: 5px 10px;
  }
  
  .member-initials {
    font-size: 2.5rem;
  }
  
  .partnership-item h3 {
    font-size: 1.2rem;
  }
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .hero-section {
    background: #000;
  }
  
  .hero-section h1,
  .hero-subtitle {
    color: #fff;
    text-shadow: 2px 2px 4px #000;
  }
  
  .expand-button {
    border: 2px solid currentColor;
  }
  
  .team-member,
  .mission-card,
  .vision-card,
  .value-card,
  .partnership-item {
    border: 2px solid #000;
  }
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
  .hero-background,
  .expand-button,
  .story-content,
  .team-grid,
  .story-details {
    animation: none;
    transition: none;
  }
  
  .expand-button:hover,
  .team-member:hover,
  .mission-card:hover,
  .vision-card:hover,
  .value-card:hover,
  .partnership-item:hover,
  .story-image:hover,
  .contact-link:hover {
    transform: none;
  }
} 