/* Global Search Component Styles - Child-Friendly Design */
@import '../styles/theme-variables.css';

/* Search Overlay */
.global-search-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  z-index: var(--z-modal);
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: 2rem;
  padding-top: 8rem;
  animation: fadeIn 0.3s ease-out;
}

.global-search-container {
  background: var(--white);
  border-radius: var(--radius-3xl);
  box-shadow: var(--shadow-2xl);
  width: 100%;
  max-width: 700px;
  max-height: 80vh;
  overflow: hidden;
  animation: slideIn 0.3s ease-out;
}

/* Search Header */
.search-header {
  padding: 2rem 2rem 1rem 2rem;
  border-bottom: 1px solid var(--gray-200);
  background: linear-gradient(135deg, var(--blue-50) 0%, var(--white) 100%);
}

.search-input-container {
  position: relative;
  display: flex;
  align-items: center;
}

.search-icon {
  position: absolute;
  left: 1rem;
  font-size: 1.5rem;
  color: var(--gray-500);
  z-index: 1;
}

.search-input {
  width: 100%;
  padding: 1rem 3rem 1rem 3.5rem;
  border: 2px solid var(--gray-300);
  border-radius: var(--radius-2xl);
  font-size: var(--text-lg);
  background: var(--white);
  transition: var(--transition-colors);
  box-shadow: var(--shadow-sm);
}

.search-input:focus {
  outline: none;
  border-color: var(--primary-blue);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.search-input::placeholder {
  color: var(--gray-500);
  font-size: var(--text-base);
}

.search-close {
  position: absolute;
  right: 1rem;
  width: 2rem;
  height: 2rem;
  border: none;
  background: var(--gray-200);
  color: var(--gray-600);
  border-radius: 50%;
  font-size: 1.2rem;
  cursor: pointer;
  transition: var(--transition-all);
  display: flex;
  align-items: center;
  justify-content: center;
}

.search-close:hover {
  background: var(--error);
  color: var(--white);
  transform: scale(1.1);
}

/* Search Results */
.search-results {
  max-height: 50vh;
  overflow-y: auto;
  padding: 1rem 0;
}

.search-loading {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  color: var(--gray-600);
  font-size: var(--text-base);
  gap: 0.5rem;
}

.loading-spinner {
  width: 1rem;
  height: 1rem;
  border: 2px solid var(--gray-300);
  border-top: 2px solid var(--primary-blue);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* No Results */
.no-results {
  text-align: center;
  padding: 3rem 2rem;
  color: var(--gray-600);
}

.no-results-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
  display: block;
}

.no-results h3 {
  color: var(--gray-700);
  margin-bottom: 1rem;
  font-size: var(--text-xl);
}

.search-suggestions {
  list-style: none;
  padding: 0;
  margin: 1rem 0 0 0;
  text-align: left;
  max-width: 300px;
  margin-left: auto;
  margin-right: auto;
}

.search-suggestions li {
  padding: 0.5rem 0;
  color: var(--gray-600);
  font-size: var(--text-sm);
}

/* Results List */
.results-list {
  padding: 0 1rem;
}

.results-count {
  padding: 0 1rem 1rem 1rem;
  color: var(--gray-600);
  font-size: var(--text-sm);
  margin: 0;
}

.search-result-item {
  padding: 1rem;
  margin: 0.5rem 0;
  border-radius: var(--radius-xl);
  cursor: pointer;
  transition: var(--transition-all);
  border: 1px solid transparent;
}

.search-result-item:hover,
.search-result-item.selected {
  background: var(--blue-50);
  border-color: var(--primary-blue);
  transform: translateX(4px);
}

.result-category {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}

.category-icon {
  font-size: 1rem;
}

.category-name {
  font-size: var(--text-xs);
  color: var(--gray-600);
  background: var(--gray-200);
  padding: 0.25rem 0.5rem;
  border-radius: var(--radius-md);
  font-weight: var(--font-medium);
}

.result-title {
  color: var(--primary-navy);
  font-size: var(--text-lg);
  font-weight: var(--font-semibold);
  margin: 0 0 0.5rem 0;
  line-height: var(--leading-tight);
}

.result-content {
  color: var(--gray-700);
  font-size: var(--text-sm);
  line-height: var(--leading-relaxed);
  margin: 0 0 0.5rem 0;
}

.result-url {
  font-size: var(--text-xs);
  color: var(--gray-500);
  font-family: var(--font-mono);
  background: var(--gray-100);
  padding: 0.25rem 0.5rem;
  border-radius: var(--radius-md);
  display: inline-block;
}

/* Default Suggestions */
.search-suggestions-default {
  padding: 2rem;
  text-align: center;
}

.search-suggestions-default h3 {
  color: var(--gray-700);
  margin-bottom: 1.5rem;
  font-size: var(--text-xl);
}

.suggestion-chips {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  justify-content: center;
}

.suggestion-chips button {
  background: var(--blue-100);
  color: var(--primary-navy);
  border: none;
  padding: 0.5rem 1rem;
  border-radius: var(--radius-full);
  font-size: var(--text-sm);
  font-weight: var(--font-medium);
  cursor: pointer;
  transition: var(--transition-all);
}

.suggestion-chips button:hover {
  background: var(--primary-blue);
  color: var(--white);
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

/* Search Footer */
.search-footer {
  padding: 1rem 2rem;
  background: var(--gray-50);
  border-top: 1px solid var(--gray-200);
  text-align: center;
}

.search-hint {
  font-size: var(--text-xs);
  color: var(--gray-600);
  margin: 0;
}

.search-hint kbd {
  background: var(--white);
  border: 1px solid var(--gray-300);
  border-radius: var(--radius-sm);
  padding: 0.125rem 0.375rem;
  font-family: var(--font-mono);
  font-size: var(--text-xs);
  margin: 0 0.125rem;
  box-shadow: var(--shadow-sm);
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-2rem) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .global-search-overlay {
    padding: 1rem;
    padding-top: 6rem;
  }

  .search-header {
    padding: 1.5rem 1.5rem 1rem 1.5rem;
  }

  .search-input {
    padding: 0.875rem 3rem 0.875rem 3rem;
    font-size: var(--text-base);
  }

  .search-input::placeholder {
    font-size: var(--text-sm);
  }

  .search-results {
    max-height: 40vh;
  }

  .results-list {
    padding: 0 0.5rem;
  }

  .search-result-item {
    padding: 0.75rem;
  }

  .result-title {
    font-size: var(--text-base);
  }

  .result-content {
    font-size: var(--text-xs);
  }

  .search-suggestions-default {
    padding: 1.5rem;
  }

  .suggestion-chips {
    gap: 0.375rem;
  }

  .suggestion-chips button {
    font-size: var(--text-xs);
    padding: 0.375rem 0.75rem;
  }
}

@media (max-width: 480px) {
  .global-search-overlay {
    padding: 0.5rem;
    padding-top: 5rem;
  }

  .global-search-container {
    border-radius: var(--radius-2xl);
    max-height: 85vh;
  }

  .search-header {
    padding: 1rem;
  }

  .search-input {
    padding: 0.75rem 2.5rem 0.75rem 2.5rem;
    font-size: var(--text-sm);
  }

  .search-icon {
    left: 0.75rem;
    font-size: 1.25rem;
  }

  .search-close {
    right: 0.75rem;
    width: 1.75rem;
    height: 1.75rem;
  }

  .search-results {
    max-height: 35vh;
  }

  .search-result-item {
    margin: 0.25rem 0;
  }

  .search-footer {
    padding: 0.75rem 1rem;
  }

  .search-hint {
    display: none; /* Hide keyboard hints on very small screens */
  }
}

/* High Contrast Mode */
@media (prefers-contrast: high) {
  .search-result-item {
    border: 2px solid var(--gray-700);
  }
  
  .search-result-item:hover,
  .search-result-item.selected {
    border-color: var(--primary-navy);
    background: var(--white);
  }
}

/* Reduced Motion */
@media (prefers-reduced-motion: reduce) {
  .global-search-overlay,
  .global-search-container,
  .search-result-item,
  .suggestion-chips button {
    animation: none;
    transition: none;
  }
  
  .search-result-item:hover,
  .search-result-item.selected {
    transform: none;
  }
  
  .suggestion-chips button:hover {
    transform: none;
  }
}
