/* Gallery Page Styles - Enhanced with Security & UX */
@import '../styles/theme-variables.css';

.gallery-page {
  padding: 2rem 0;
  background-color: var(--bg-primary);
  min-height: 100vh;
}

.gallery-page h1 {
  text-align: center;
  color: var(--primary-navy);
  margin-bottom: 3rem;
  font-size: var(--text-6xl);
  font-weight: var(--font-extrabold);
  line-height: var(--leading-tight);
}

/* Enhanced Gallery Controls */
.gallery-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding: 0 2rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.search-container {
  position: relative;
  flex: 1;
  max-width: 400px;
}

.search-input {
  width: 100%;
  padding: 0.75rem 2.5rem 0.75rem 1rem;
  border: 2px solid var(--gray-300);
  border-radius: var(--radius-xl);
  font-size: var(--text-base);
  transition: var(--transition-colors);
  background: var(--white);
}

.search-input:focus {
  outline: none;
  border-color: var(--primary-blue);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.search-icon {
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  color: var(--gray-500);
  pointer-events: none;
}

.view-controls {
  display: flex;
  gap: 0.5rem;
  background: var(--gray-100);
  padding: 0.25rem;
  border-radius: var(--radius-lg);
}

.view-btn {
  padding: 0.5rem 1rem;
  border: none;
  background: transparent;
  color: var(--gray-600);
  border-radius: var(--radius-md);
  cursor: pointer;
  transition: var(--transition-all);
  font-size: 1.2rem;
}

.view-btn:hover {
  background: var(--white);
  color: var(--primary-blue);
}

.view-btn.active {
  background: var(--primary-blue);
  color: var(--white);
  box-shadow: var(--shadow-sm);
}

.results-info {
  text-align: center;
  margin-bottom: 2rem;
  color: var(--gray-600);
  font-size: var(--text-sm);
}

/* Enhanced Category Filter */
.category-filter {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 3rem;
  padding: 0 2rem;
}

.category-btn {
  background: var(--white);
  color: var(--primary-navy);
  border: 2px solid var(--primary-navy);
  padding: 0.75rem 1.5rem;
  border-radius: var(--radius-full);
  font-weight: var(--font-semibold);
  cursor: pointer;
  transition: var(--transition-all);
  font-size: var(--text-base);
  position: relative;
  overflow: hidden;
}

.category-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, var(--primary-navy) 0%, var(--secondary-blue) 100%);
  transition: left 0.3s ease;
  z-index: -1;
}

.category-btn:hover::before,
.category-btn.active::before {
  left: 0;
}

.category-btn:hover,
.category-btn.active {
  color: var(--white);
  transform: translateY(-2px);
  box-shadow: var(--shadow-brand);
}

.category-btn:focus {
  outline: 2px solid var(--primary-blue);
  outline-offset: 2px;
}

/* Enhanced Gallery Grid */
.gallery-grid {
  margin-bottom: 4rem;
}

.grid-layout {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: var(--gallery-grid-gap);
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 2rem;
}

.masonry-layout {
  columns: 3;
  column-gap: var(--gallery-grid-gap);
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 2rem;
}

.masonry-layout .gallery-item {
  break-inside: avoid;
  margin-bottom: var(--gallery-grid-gap);
  display: inline-block;
  width: 100%;
}

/* Image Gallery */
.image-gallery {
  margin-bottom: 4rem;
}

.image-gallery h2 {
  text-align: center;
  color: #0f4c75;
  margin-bottom: 3rem;
  font-size: 2.5rem;
  font-weight: 700;
}

.gallery-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 2rem;
}

/* Masonry-style layout for varied image heights */
@media (min-width: 1024px) {
  .gallery-grid {
    columns: 3;
    column-gap: 2rem;
  }
  
  .gallery-item {
    break-inside: avoid;
    margin-bottom: 2rem;
  }
}

/* Enhanced Gallery Items */
.gallery-item {
  background: var(--white);
  border-radius: var(--gallery-item-radius);
  box-shadow: var(--shadow-lg);
  overflow: hidden;
  transition: var(--transition-all);
  cursor: pointer;
  position: relative;
  border: 1px solid var(--gray-200);
}

.gallery-item:hover {
  transform: translateY(-8px) scale(var(--gallery-hover-scale));
  box-shadow: var(--shadow-brand-lg);
}

.gallery-item:focus {
  outline: 3px solid var(--primary-blue);
  outline-offset: 2px;
}

.image-container {
  position: relative;
  overflow: hidden;
}

.image-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    to bottom,
    transparent 0%,
    rgba(0, 0, 0, 0.7) 100%
  );
  opacity: 0;
  transition: var(--transition-opacity);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 1rem;
  color: var(--white);
}

.gallery-item:hover .image-overlay {
  opacity: 1;
}

.zoom-icon {
  font-size: 2rem;
  text-align: center;
  margin: auto;
  background: rgba(255, 255, 255, 0.2);
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(10px);
  transition: var(--transition-transform);
}

.gallery-item:hover .zoom-icon {
  transform: scale(1.1);
}

.image-info {
  text-align: right;
}

.image-index {
  font-size: var(--text-sm);
  opacity: 0.9;
  background: rgba(0, 0, 0, 0.5);
  padding: 0.25rem 0.5rem;
  border-radius: var(--radius-md);
}

/* No Results State */
.no-results {
  text-align: center;
  padding: 4rem 2rem;
  color: var(--gray-600);
}

.no-results p {
  font-size: var(--text-lg);
  margin-bottom: 2rem;
}

.reset-filters-btn {
  background: var(--primary-blue);
  color: var(--white);
  border: none;
  padding: 0.75rem 2rem;
  border-radius: var(--radius-lg);
  font-weight: var(--font-semibold);
  cursor: pointer;
  transition: var(--transition-all);
}

.reset-filters-btn:hover {
  background: var(--primary-navy);
  transform: translateY(-2px);
  box-shadow: var(--shadow-brand);
}

.gallery-item img {
  width: 100%;
  height: 300px;
  object-fit: cover;
  transition: transform 0.4s ease;
  opacity: 0;
}

.gallery-item img.loaded {
  opacity: 1;
}

.gallery-item:hover img {
  transform: scale(1.1);
}

/* Responsive image heights */
@media (min-width: 768px) {
  .gallery-item img {
    height: 350px;
  }
}

@media (min-width: 1024px) {
  .gallery-item img {
    height: 400px;
  }
}

.gallery-caption {
  padding: 1.5rem;
  color: #495057;
  font-size: 1rem;
  line-height: 1.6;
  text-align: center;
  background: white;
  position: relative;
  z-index: 1;
}

.gallery-caption p {
  margin: 0;
  font-weight: 500;
  color: #0f4c75;
  transition: color 0.3s ease;
}

.gallery-item:hover .gallery-caption p {
  color: #3282b8;
}

/* Video Gallery */
.video-gallery {
  margin-bottom: 4rem;
}

.video-gallery h2 {
  text-align: center;
  color: #001f4c;
  margin-bottom: 3rem;
  font-size: 2.5rem;
}

.video-item {
  position: relative;
}

.video-thumbnail {
  position: relative;
}

.play-button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60px;
  height: 60px;
  background: rgba(0, 31, 76, 0.9);
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  transition: all 0.3s ease;
  z-index: 2;
}

.video-item:hover .play-button {
  background: #FFD700;
  color: #001f4c;
  transform: translate(-50%, -50%) scale(1.1);
}

/* Loading Animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.gallery-item {
  animation: fadeIn 0.6s ease-out;
}

/* Enhanced Lightbox */
.lightbox {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--modal-bg);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: var(--z-modal);
  padding: 1rem;
  backdrop-filter: blur(15px);
  animation: fadeIn 0.3s ease-out;
}

.lightbox-content {
  position: relative;
  max-width: 95vw;
  max-height: 95vh;
  background: var(--white);
  border-radius: var(--modal-radius);
  overflow: hidden;
  box-shadow: var(--modal-shadow);
  animation: slideIn 0.3s ease-out;
  display: flex;
  flex-direction: column;
}

.lightbox-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1.5rem;
  background: var(--gray-50);
  border-bottom: 1px solid var(--gray-200);
}

.lightbox-counter {
  font-weight: var(--font-semibold);
  color: var(--gray-700);
  font-size: var(--text-sm);
}

.close-lightbox {
  width: 40px;
  height: 40px;
  background: var(--error);
  color: var(--white);
  border: none;
  border-radius: 50%;
  font-size: 1.5rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: var(--transition-all);
  z-index: var(--z-tooltip);
}

.close-lightbox:hover {
  background: var(--accent-gold);
  color: var(--primary-navy);
  transform: scale(1.1);
}

.lightbox-media-container {
  position: relative;
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--gray-900);
  min-height: 400px;
}

.lightbox-media {
  max-width: 100%;
  max-height: 70vh;
  object-fit: contain;
  display: block;
}

/* Navigation Arrows */
.nav-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 50px;
  height: 50px;
  background: rgba(255, 255, 255, 0.2);
  color: var(--white);
  border: none;
  border-radius: 50%;
  font-size: 2rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: var(--transition-all);
  backdrop-filter: blur(10px);
  z-index: var(--z-popover);
}

.nav-arrow:hover {
  background: var(--accent-gold);
  color: var(--primary-navy);
  transform: translateY(-50%) scale(1.1);
}

.nav-prev {
  left: 1rem;
}

.nav-next {
  right: 1rem;
}

.lightbox-info {
  padding: 1.5rem;
  background: var(--white);
}

.lightbox-caption {
  color: var(--gray-700);
  font-size: var(--text-base);
  line-height: var(--leading-relaxed);
  margin-bottom: 1rem;
  text-align: center;
}

.lightbox-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: var(--text-sm);
  color: var(--gray-600);
}

.media-category {
  background: var(--primary-blue);
  color: var(--white);
  padding: 0.25rem 0.75rem;
  border-radius: var(--radius-full);
  font-weight: var(--font-medium);
}

.navigation-hint {
  font-style: italic;
  opacity: 0.8;
}

/* Thumbnail Navigation */
.lightbox-thumbnails {
  display: flex;
  gap: 0.5rem;
  padding: 1rem;
  background: var(--gray-50);
  border-top: 1px solid var(--gray-200);
  justify-content: center;
  overflow-x: auto;
}

.thumbnail {
  width: 60px;
  height: 60px;
  border: 2px solid transparent;
  border-radius: var(--radius-md);
  cursor: pointer;
  overflow: hidden;
  transition: var(--transition-all);
  flex-shrink: 0;
}

.thumbnail.active {
  border-color: var(--primary-blue);
  box-shadow: 0 0 0 2px rgba(59, 130, 246, 0.3);
}

.thumbnail:hover {
  transform: scale(1.1);
  border-color: var(--accent-gold);
}

.thumbnail img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Enhanced Responsive Design */
@media (max-width: 1024px) {
  .masonry-layout {
    columns: 2;
  }
  
  .grid-layout {
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  }
}

@media (max-width: 768px) {
  .gallery-page h1 {
    font-size: var(--text-4xl);
  }
  
  .gallery-controls {
    flex-direction: column;
    align-items: stretch;
    gap: 1rem;
    padding: 0 1rem;
  }
  
  .search-container {
    max-width: none;
  }
  
  .view-controls {
    align-self: center;
  }
  
  .category-filter {
    gap: 0.5rem;
    padding: 0 1rem;
  }
  
  .category-btn {
    padding: 0.5rem 1rem;
    font-size: var(--text-sm);
  }
  
  .grid-layout,
  .masonry-layout {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: var(--gallery-grid-gap-mobile);
    padding: 0 1rem;
    columns: 1;
  }
  
  .gallery-item img {
    height: 250px;
  }
  
  .gallery-caption {
    padding: 1rem;
    font-size: var(--text-sm);
  }
  
  .zoom-icon {
    width: 50px;
    height: 50px;
    font-size: 1.5rem;
  }
  
  .lightbox {
    padding: 0.5rem;
  }
  
  .lightbox-content {
    max-width: 98vw;
    max-height: 98vh;
  }
  
  .lightbox-header {
    padding: 0.75rem 1rem;
  }
  
  .close-lightbox {
    width: 35px;
    height: 35px;
    font-size: 1.2rem;
  }
  
  .nav-arrow {
    width: 40px;
    height: 40px;
    font-size: 1.5rem;
  }
  
  .nav-prev {
    left: 0.5rem;
  }
  
  .nav-next {
    right: 0.5rem;
  }
  
  .lightbox-thumbnails {
    padding: 0.5rem;
    gap: 0.25rem;
  }
  
  .thumbnail {
    width: 50px;
    height: 50px;
  }
  
  .lightbox-info {
    padding: 1rem;
  }
  
  .lightbox-meta {
    flex-direction: column;
    gap: 0.5rem;
    align-items: flex-start;
  }
}

@media (max-width: 480px) {
  .gallery-page {
    padding: 1rem 0;
  }
  
  .gallery-page h1 {
    font-size: var(--text-3xl);
    margin-bottom: 2rem;
  }
  
  .gallery-controls {
    padding: 0 0.5rem;
  }
  
  .category-filter {
    padding: 0 0.5rem;
  }
  
  .category-btn {
    padding: 0.4rem 0.8rem;
    font-size: var(--text-xs);
  }
  
  .grid-layout,
  .masonry-layout {
    grid-template-columns: 1fr;
    gap: 1rem;
    padding: 0 0.5rem;
  }
  
  .gallery-item img {
    height: 200px;
  }
  
  .gallery-caption {
    padding: 0.75rem;
    font-size: var(--text-xs);
  }
  
  .zoom-icon {
    width: 40px;
    height: 40px;
    font-size: 1.2rem;
  }
  
  .lightbox {
    padding: 0.25rem;
  }
  
  .lightbox-content {
    max-width: 99vw;
    max-height: 99vh;
  }
  
  .lightbox-header {
    padding: 0.5rem 0.75rem;
  }
  
  .lightbox-counter {
    font-size: var(--text-xs);
  }
  
  .close-lightbox {
    width: 30px;
    height: 30px;
    font-size: 1rem;
  }
  
  .nav-arrow {
    width: 35px;
    height: 35px;
    font-size: 1.2rem;
  }
  
  .nav-prev {
    left: 0.25rem;
  }
  
  .nav-next {
    right: 0.25rem;
  }
  
  .lightbox-media {
    max-height: 60vh;
  }
  
  .lightbox-info {
    padding: 0.75rem;
  }
  
  .lightbox-caption {
    font-size: var(--text-sm);
  }
  
  .lightbox-thumbnails {
    padding: 0.25rem;
    gap: 0.125rem;
  }
  
  .thumbnail {
    width: 40px;
    height: 40px;
  }
  
  .no-results {
    padding: 2rem 1rem;
  }
  
  .reset-filters-btn {
    padding: 0.5rem 1.5rem;
    font-size: var(--text-sm);
  }
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .gallery-item {
    border: 2px solid #000000;
  }
  
  .play-button {
    border: 2px solid #ffffff;
  }
  
  .close-button {
    border: 2px solid #ffffff;
  }
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
  .gallery-item:hover {
    transform: none;
  }
  
  .gallery-item:hover img {
    transform: none;
  }
  
  .video-item:hover .play-button {
    transform: translate(-50%, -50%);
  }
  
  .close-button:hover {
    transform: none;
  }
}

/* Print styles */
@media print {
  .gallery-item {
    break-inside: avoid;
    border: 1px solid #000000;
    box-shadow: none;
  }
  
  .gallery-item img {
    height: 200px;
  }
  
  .play-button {
    display: none;
  }
  
  .lightbox-overlay {
    display: none;
  }
} 