/* Import Unified Theme Variables */
@import './styles/theme-variables.css';

/* Global CSS Reset and Base Styles - Cross-Platform Compatible */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Ensure consistent rendering across all browsers and OS */
html {
  scroll-behavior: smooth;
  font-size: 16px;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Cross-platform font rendering optimization */
body {
  -webkit-font-feature-settings: "kern" 1;
  font-feature-settings: "kern" 1;
  text-rendering: optimizeLegibility;
  -webkit-font-variant-ligatures: common-ligatures;
  font-variant-ligatures: common-ligatures;
}

/* Professional Typography System - Cross-Platform Optimized */
:root {
  /* Font Family - Comprehensive cross-platform fallbacks */
  --font-primary: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Helvetica Neue', Arial, 'Noto Sans', 'Liberation Sans', sans-serif;
  --font-mono: 'SF Mono', Monaco, 'Cascadia Code', 'Roboto Mono', Consolas, 'Courier New', 'Liberation Mono', monospace;
  
  /* Font Weights - Semantic Names */
  --font-light: 300;      /* Subtle text, captions */
  --font-normal: 400;     /* Body text, paragraphs */
  --font-medium: 500;     /* Navigation, buttons */
  --font-semibold: 600;   /* Subheadings, emphasis */
  --font-bold: 700;       /* Headings, important text */
  --font-extrabold: 800;  /* Hero titles, main headings */
  --font-black: 900;      /* Display text, impact */
  
  /* Typography Scale - Modular Scale 1.25 (Major Third) */
  --text-xs: 0.75rem;     /* 12px */
  --text-sm: 0.875rem;    /* 14px */
  --text-base: 1rem;      /* 16px */
  --text-lg: 1.125rem;    /* 18px */
  --text-xl: 1.25rem;     /* 20px */
  --text-2xl: 1.5rem;     /* 24px */
  --text-3xl: 1.875rem;   /* 30px */
  --text-4xl: 2.25rem;    /* 36px */
  --text-5xl: 3rem;       /* 48px */
  --text-6xl: 3.75rem;    /* 60px */
  --text-7xl: 4.5rem;     /* 72px */
  --text-8xl: 6rem;       /* 96px */
  --text-9xl: 8rem;       /* 128px */
  
  /* Responsive Typography Scales */
  --hero-title: clamp(2.5rem, 8vw, 6rem);
  --section-title: clamp(2rem, 6vw, 4rem);
  --subsection-title: clamp(1.5rem, 4vw, 2.5rem);
  --body-large: clamp(1.1rem, 2.5vw, 1.3rem);
  --body-normal: clamp(1rem, 2vw, 1.1rem);
  --body-small: clamp(0.9rem, 1.8vw, 1rem);
  
  /* Line Heights - Optimized for Readability */
  --leading-none: 1;      /* Headings */
  --leading-tight: 1.25;  /* Subheadings */
  --leading-snug: 1.375;  /* Large text */
  --leading-normal: 1.5;  /* Body text */
  --leading-relaxed: 1.625; /* Long paragraphs */
  --leading-loose: 2;     /* Spacious text */
  
  /* Letter Spacing */
  --tracking-tight: -0.025em;
  --tracking-normal: 0;
  --tracking-wide: 0.025em;
  --tracking-wider: 0.05em;
  --tracking-widest: 0.1em;
  
  /* Responsive Scale Factors */
  --text-scale: 1;
  --line-height-scale: 1.1;
}

/* Comprehensive Responsive Breakpoints - All Screen Sizes */
/* Extra Small devices (phones, 320px and up) */
@media (min-width: 320px) {
  :root {
    --text-scale: 0.9;
    --line-height-scale: 1.2;
  }
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
  :root {
    --text-scale: 0.95;
    --line-height-scale: 1.15;
  }
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
  :root {
    --text-scale: 1.1;
    --line-height-scale: 1.05;
  }
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
  :root {
    --text-scale: 1.15;
    --line-height-scale: 1.02;
  }
}

/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
  :root {
    --text-scale: 1.2;
    --line-height-scale: 1;
  }
}

/* 2K displays (1440px and up) */
@media (min-width: 1440px) {
  :root {
    --text-scale: 1.3;
    --line-height-scale: 0.95;
  }
}

/* 4K displays (2560px and up) */
@media (min-width: 2560px) {
  :root {
    --text-scale: 1.4;
    --line-height-scale: 0.9;
  }
}

/* Ultra-wide displays (3440px and up) */
@media (min-width: 3440px) {
  :root {
    --text-scale: 1.5;
    --line-height-scale: 0.85;
  }
}

body {
  font-family: var(--font-primary);
  font-size: var(--text-base);
  font-weight: var(--font-normal);
  line-height: var(--leading-normal);
  color: #1f2937;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  font-feature-settings: "kern" 1, "liga" 1, "calt" 1;
}

/* Enhanced Typography System */
h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-primary);
  font-weight: var(--font-bold);
  line-height: var(--leading-tight);
  margin-bottom: 1.5rem;
  color: #0f4c75;
  letter-spacing: var(--tracking-tight);
  text-rendering: optimizeLegibility;
}

h1 { 
  font-size: var(--hero-title);
  font-weight: var(--font-black);
  line-height: var(--leading-none);
  letter-spacing: var(--tracking-tight);
  margin-bottom: 2rem;
}

h2 { 
  font-size: var(--section-title);
  font-weight: var(--font-bold);
  line-height: var(--leading-tight);
  margin-bottom: 1.5rem;
}

h3 { 
  font-size: var(--subsection-title);
  font-weight: var(--font-semibold);
  line-height: var(--leading-snug);
  margin-bottom: 1.25rem;
}

h4 { 
  font-size: var(--text-2xl);
  font-weight: var(--font-semibold);
  line-height: var(--leading-snug);
  margin-bottom: 1rem;
}

h5 { 
  font-size: var(--text-xl);
  font-weight: var(--font-medium);
  line-height: var(--leading-normal);
  margin-bottom: 0.75rem;
}

h6 { 
  font-size: var(--text-lg);
  font-weight: var(--font-medium);
  line-height: var(--leading-normal);
  margin-bottom: 0.5rem;
}

p {
  font-size: var(--body-normal);
  font-weight: var(--font-normal);
  line-height: var(--leading-relaxed);
  margin-bottom: 1.5rem;
  color: #4b5563;
  max-width: 70ch;
}

/* Typography Utility Classes */
.text-xs { font-size: var(--text-xs); }
.text-sm { font-size: var(--text-sm); }
.text-base { font-size: var(--text-base); }
.text-lg { font-size: var(--text-lg); }
.text-xl { font-size: var(--text-xl); }
.text-2xl { font-size: var(--text-2xl); }
.text-3xl { font-size: var(--text-3xl); }
.text-4xl { font-size: var(--text-4xl); }
.text-5xl { font-size: var(--text-5xl); }
.text-6xl { font-size: var(--text-6xl); }
.text-7xl { font-size: var(--text-7xl); }
.text-8xl { font-size: var(--text-8xl); }
.text-9xl { font-size: var(--text-9xl); }

.font-light { font-weight: var(--font-light); }
.font-normal { font-weight: var(--font-normal); }
.font-medium { font-weight: var(--font-medium); }
.font-semibold { font-weight: var(--font-semibold); }
.font-bold { font-weight: var(--font-bold); }
.font-extrabold { font-weight: var(--font-extrabold); }
.font-black { font-weight: var(--font-black); }

.leading-none { line-height: var(--leading-none); }
.leading-tight { line-height: var(--leading-tight); }
.leading-snug { line-height: var(--leading-snug); }
.leading-normal { line-height: var(--leading-normal); }
.leading-relaxed { line-height: var(--leading-relaxed); }
.leading-loose { line-height: var(--leading-loose); }

.tracking-tight { letter-spacing: var(--tracking-tight); }
.tracking-normal { letter-spacing: var(--tracking-normal); }
.tracking-wide { letter-spacing: var(--tracking-wide); }
.tracking-wider { letter-spacing: var(--tracking-wider); }
.tracking-widest { letter-spacing: var(--tracking-widest); }

/* Links */
a {
  color: #3b82f6;
  text-decoration: none;
  transition: all 0.3s ease;
}

a:hover {
  color: #1e3a8a;
  text-decoration: underline;
}

/* Enhanced Button Styles */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  font-family: var(--font-primary);
  font-size: var(--text-base);
  font-weight: var(--font-semibold);
  line-height: var(--leading-tight);
  letter-spacing: var(--tracking-wide);
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  min-height: 44px; /* Accessibility requirement */
  text-rendering: optimizeLegibility;
}

.btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s ease;
}

.btn:hover::before {
  left: 100%;
}

/* Button Variants */
.btn-primary {
  background: linear-gradient(135deg, #3b82f6 0%, #1e3a8a 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(59, 130, 246, 0.3);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(59, 130, 246, 0.4);
  background: linear-gradient(135deg, #2563eb 0%, #1e40af 100%);
}

.btn-secondary {
  background: linear-gradient(135deg, #f59e0b 0%, #d97706 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(245, 158, 11, 0.3);
}

.btn-secondary:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(245, 158, 11, 0.4);
  background: linear-gradient(135deg, #d97706 0%, #b45309 100%);
}

.btn-success {
  background: linear-gradient(135deg, #059669 0%, #047857 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(5, 150, 105, 0.3);
}

.btn-success:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(5, 150, 105, 0.4);
  background: linear-gradient(135deg, #047857 0%, #065f46 100%);
}

.btn-outline {
  background: transparent;
  color: #3b82f6;
  border: 2px solid #3b82f6;
  box-shadow: 0 4px 15px rgba(59, 130, 246, 0.1);
}

.btn-outline:hover {
  background: #3b82f6;
  color: white;
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(59, 130, 246, 0.3);
}

.btn-ghost {
  background: transparent;
  color: #6b7280;
  border: 1px solid #d1d5db;
}

.btn-ghost:hover {
  background: #f9fafb;
  color: #374151;
  border-color: #9ca3af;
  transform: translateY(-1px);
}

/* Button Sizes */
.btn-sm {
  padding: 8px 16px;
  font-size: var(--text-sm);
  font-weight: var(--font-medium);
  min-height: 36px;
}

.btn-lg {
  padding: 16px 32px;
  font-size: var(--text-lg);
  font-weight: var(--font-bold);
  min-height: 52px;
}

/* Button States */
.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none !important;
}

.btn:active {
  transform: translateY(0);
}

/* Form Elements */
input, textarea, select {
  width: 100%;
  padding: 12px 16px;
  border: 2px solid #e5e7eb;
  border-radius: 8px;
  font-family: var(--font-primary);
  font-size: var(--text-base);
  font-weight: var(--font-normal);
  line-height: var(--leading-normal);
  transition: all 0.3s ease;
  background: white;
  text-rendering: optimizeLegibility;
}

input:focus, textarea:focus, select:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

input:invalid, textarea:invalid, select:invalid {
  border-color: #ef4444;
}

/* Enhanced Card Styles */
.card {
  background: white;
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
  border: 1px solid #f3f4f6;
}

.card:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 25px rgba(0, 0, 0, 0.1);
}

/* Utility Classes */
.text-center { text-align: center; }
.text-left { text-align: left; }
.text-right { text-align: right; }

.mb-1 { margin-bottom: 0.25rem; }
.mb-2 { margin-bottom: 0.5rem; }
.mb-3 { margin-bottom: 0.75rem; }
.mb-4 { margin-bottom: 1rem; }
.mb-5 { margin-bottom: 1.25rem; }
.mb-6 { margin-bottom: 1.5rem; }

.mt-1 { margin-top: 0.25rem; }
.mt-2 { margin-top: 0.5rem; }
.mt-3 { margin-top: 0.75rem; }
.mt-4 { margin-top: 1rem; }
.mt-5 { margin-top: 1.25rem; }
.mt-6 { margin-top: 1.5rem; }

.p-1 { padding: 0.25rem; }
.p-2 { padding: 0.5rem; }
.p-3 { padding: 0.75rem; }
.p-4 { padding: 1rem; }
.p-5 { padding: 1.25rem; }
.p-6 { padding: 1.5rem; }

.flex { display: flex; }
.flex-col { flex-direction: column; }
.items-center { align-items: center; }
.justify-center { justify-content: center; }
.justify-between { justify-content: space-between; }

.grid { display: grid; }
.gap-2 { gap: 0.5rem; }
.gap-3 { gap: 0.75rem; }
.gap-4 { gap: 1rem; }
.gap-5 { gap: 1.25rem; }
.gap-6 { gap: 1.5rem; }

.w-full { width: 100%; }
.h-full { height: 100%; }

.rounded { border-radius: 0.375rem; }
.rounded-lg { border-radius: 0.5rem; }
.rounded-xl { border-radius: 0.75rem; }
.rounded-2xl { border-radius: 1rem; }

.shadow { box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06); }
.shadow-lg { box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05); }
.shadow-xl { box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04); }

/* Responsive Design */
@media (max-width: 768px) {
  .btn {
    padding: 10px 20px;
    font-size: var(--text-sm);
  }
  
  .btn-lg {
    padding: 14px 28px;
    font-size: var(--text-base);
  }
  
  .card {
    padding: 20px;
  }
  
  h1 { font-size: clamp(2rem, 6vw, 4rem); }
  h2 { font-size: clamp(1.5rem, 4vw, 2.5rem); }
  h3 { font-size: clamp(1.25rem, 3vw, 2rem); }
  p { font-size: var(--body-small); }
}

@media (max-width: 480px) {
  .btn {
    padding: 8px 16px;
    font-size: var(--text-xs);
  }
  
  .card {
    padding: 16px;
  }
  
  h1 { font-size: clamp(1.75rem, 5vw, 3rem); }
  h2 { font-size: clamp(1.25rem, 3.5vw, 2rem); }
  h3 { font-size: clamp(1.1rem, 2.5vw, 1.5rem); }
  p { font-size: var(--text-sm); }
}

/* High Contrast Mode Support */
@media (prefers-contrast: high) {
  .btn {
    border: 2px solid currentColor;
  }
  
  .card {
    border: 2px solid #000;
  }
  
  input, textarea, select {
    border: 2px solid #000;
  }
}

/* Reduced Motion Support */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
  
  .btn:hover {
    transform: none;
  }
  
  .card:hover {
    transform: none;
  }
}

/* Focus Styles for Accessibility */
.btn:focus,
input:focus,
textarea:focus,
select:focus {
  outline: 2px solid #3b82f6;
  outline-offset: 2px;
}

/* Print Styles */
@media print {
  .btn,
  .card {
    box-shadow: none;
    border: 1px solid #000;
  }
  
  .btn:hover {
    transform: none;
  }
  
  .card:hover {
    transform: none;
  }
}

/* ===== CROSS-PLATFORM COMPATIBILITY ===== */

/* iOS Safari Specific Fixes */
@supports (-webkit-touch-callout: none) {
  /* Prevent zoom on input focus in iOS */
  input, textarea, select {
    font-size: 16px;
  }
  
  /* Fix for iOS Safari viewport height */
  .vh-100 {
    height: 100vh;
    height: -webkit-fill-available;
  }
}

/* Android Chrome Specific Fixes */
@supports (-webkit-appearance: none) and (not (overflow: -webkit-marquee)) {
  /* Fix for Android Chrome input styling */
  input, textarea, select {
    -webkit-appearance: none;
    appearance: none;
    border-radius: 0;
  }
}

/* Windows High Contrast Mode */
@media (prefers-contrast: high) {
  * {
    border-color: currentColor !important;
  }
  
  .btn {
    border: 2px solid currentColor !important;
    background: transparent !important;
    color: currentColor !important;
  }
  
  .card {
    border: 2px solid currentColor !important;
    background: transparent !important;
  }
  
  input, textarea, select {
    border: 2px solid currentColor !important;
    background: transparent !important;
    color: currentColor !important;
  }
}

/* Dark Mode Support */
@media (prefers-color-scheme: dark) {
  :root {
    --text-color: #e5e7eb;
    --bg-color: #1f2937;
    --card-bg: #374151;
    --border-color: #4b5563;
  }
  
  body {
    color: var(--text-color);
    background-color: var(--bg-color);
  }
  
  .card {
    background-color: var(--card-bg);
    border-color: var(--border-color);
  }
  
  input, textarea, select {
    background-color: var(--card-bg);
    border-color: var(--border-color);
    color: var(--text-color);
  }
}

/* Landscape Orientation Support */
@media (orientation: landscape) and (max-height: 500px) {
  .hero-section {
    min-height: 100vh;
    padding: 2rem 0;
  }
  
  .btn {
    padding: 8px 16px;
    font-size: var(--text-sm);
  }
}

/* Portrait Orientation Support */
@media (orientation: portrait) and (max-width: 768px) {
  .hero-section {
    min-height: 100vh;
    padding: 4rem 0;
  }
}

/* Touch Device Optimizations */
@media (hover: none) and (pointer: coarse) {
  /* Larger touch targets */
  .btn {
    min-height: 44px;
    min-width: 44px;
    padding: 12px 24px;
  }
  
  /* Remove hover effects on touch devices */
  .btn:hover,
  .card:hover {
    transform: none;
  }
  
  /* Active state for touch feedback */
  .btn:active {
    transform: scale(0.98);
  }
}

/* Non-touch Device Optimizations */
@media (hover: hover) and (pointer: fine) {
  .btn:hover {
    transform: translateY(-2px);
  }
  
  .card:hover {
    transform: translateY(-4px);
  }
}

/* Ultra-wide Screen Support */
@media (min-width: 2000px) {
  .container {
    max-width: 1800px;
    margin: 0 auto;
  }
  
  .hero-section {
    padding: 6rem 0;
  }
}

/* Small Screen Optimizations */
@media (max-width: 360px) {
  .btn {
    padding: 6px 12px;
    font-size: var(--text-xs);
  }
  
  .card {
    padding: 12px;
  }
  
  h1 { font-size: clamp(1.5rem, 4vw, 2.5rem); }
  h2 { font-size: clamp(1.1rem, 3vw, 1.8rem); }
  h3 { font-size: clamp(1rem, 2.5vw, 1.4rem); }
}

/* High DPI Display Support */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .btn {
    border-width: 0.5px;
  }
  
  .card {
    border-width: 0.5px;
  }
}

/* Low Bandwidth Support */
@media (prefers-reduced-data: reduce) {
  * {
    animation-duration: 0.01ms !important;
    transition-duration: 0.01ms !important;
  }
  
  /* Note: loading="lazy" should be applied as HTML attribute, not CSS property */
  img {
    /* CSS properties for lazy loading optimization */
    content-visibility: auto;
    contain-intrinsic-size: 0 500px;
  }
}

/* Screen Reader Support */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* Focus Visible Support */
.btn:focus-visible,
input:focus-visible,
textarea:focus-visible,
select:focus-visible {
  outline: 2px solid #3b82f6;
  outline-offset: 2px;
}

/* Skip Link for Accessibility */
.skip-link {
  position: absolute;
  top: -40px;
  left: 6px;
  background: #000;
  color: white;
  padding: 8px;
  text-decoration: none;
  z-index: 10000;
}

.skip-link:focus {
  top: 6px;
}

/* Container Responsive System */
.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 1rem;
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}

@media (min-width: 1400px) {
  .container {
    max-width: 1320px;
  }
}

/* Grid System for All Screen Sizes */
.grid-responsive {
  display: grid;
  gap: 1rem;
  grid-template-columns: 1fr;
}

@media (min-width: 576px) {
  .grid-responsive {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 768px) {
  .grid-responsive {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 992px) {
  .grid-responsive {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (min-width: 1200px) {
  .grid-responsive {
    grid-template-columns: repeat(5, 1fr);
  }
}

/* Utility Classes for Cross-Platform Support */
.hidden-xs { display: none; }
.hidden-sm { display: none; }
.hidden-md { display: none; }
.hidden-lg { display: none; }
.hidden-xl { display: none; }

@media (min-width: 576px) {
  .hidden-xs { display: block; }
  .hidden-sm { display: none; }
}

@media (min-width: 768px) {
  .hidden-sm { display: block; }
  .hidden-md { display: none; }
}

@media (min-width: 992px) {
  .hidden-md { display: block; }
  .hidden-lg { display: none; }
}

@media (min-width: 1200px) {
  .hidden-lg { display: block; }
  .hidden-xl { display: none; }
}

@media (min-width: 1400px) {
  .hidden-xl { display: block; }
}

/* Safe Area Support for Notched Devices */
@supports (padding: max(0px)) {
  .safe-area-top {
    padding-top: max(1rem, env(safe-area-inset-top));
  }
  
  .safe-area-bottom {
    padding-bottom: max(1rem, env(safe-area-inset-bottom));
  }
  
  .safe-area-left {
    padding-left: max(1rem, env(safe-area-inset-left));
  }
  
  .safe-area-right {
    padding-right: max(1rem, env(safe-area-inset-right));
  }
} 