/* Footer Styles - Unified Theme */
.footer {
  background: linear-gradient(135deg, #0f4c75 0%, #1e3a8a 50%, #3b82f6 100%);
  color: white;
  padding: 60px 0 20px;
  margin-top: auto;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 40px;
  padding: 0 20px;
}

.footer-section h3 {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 20px;
  color: white;
}

.footer-section h4 {
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 15px;
  color: white;
}

.footer-section p {
  line-height: 1.6;
  margin-bottom: 10px;
  color: rgba(255, 255, 255, 0.9);
}

.footer-section ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-section ul li {
  margin-bottom: 8px;
}

.footer-section ul li a {
  color: rgba(255, 255, 255, 0.9);
  text-decoration: none;
  transition: all 0.3s ease;
  display: inline-block;
}

.footer-section ul li a:hover {
  color: #FFD700;
  transform: translateX(5px);
}

/* Social Media Icons */
.social-links {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
}

.social-links a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 45px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  color: white;
  text-decoration: none;
  transition: all 0.3s ease;
  border: 2px solid transparent;
  backdrop-filter: blur(10px);
}

.social-links a:hover {
  background: rgba(255, 215, 0, 0.2);
  transform: translateY(-3px);
  border-color: rgba(255, 215, 0, 0.5);
  box-shadow: 0 8px 25px rgba(255, 215, 0, 0.3);
  color: #FFD700;
}

.social-links a svg {
  width: 20px;
  height: 20px;
  transition: transform 0.3s ease;
}

.social-links a:hover svg {
  transform: scale(1.1);
}

/* Footer Bottom */
.footer-bottom {
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  margin-top: 40px;
  padding-top: 20px;
  text-align: center;
}

.footer-bottom p {
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.9rem;
  margin: 0;
}

/* Responsive Design */
@media (max-width: 768px) {
  .footer {
    padding: 40px 0 20px;
  }
  
  .footer-content {
    grid-template-columns: 1fr;
    gap: 30px;
    padding: 0 15px;
  }
  
  .footer-section h3 {
    font-size: 1.3rem;
  }
  
  .footer-section h4 {
    font-size: 1.1rem;
  }
  
  .social-links {
    justify-content: center;
  }
  
  .social-links a {
    width: 40px;
    height: 40px;
  }
  
  .social-links a svg {
    width: 18px;
    height: 18px;
  }
}

@media (max-width: 480px) {
  .footer {
    padding: 30px 0 15px;
  }
  
  .footer-content {
    padding: 0 10px;
  }
  
  .footer-section h3 {
    font-size: 1.2rem;
  }
  
  .footer-section h4 {
    font-size: 1rem;
  }
  
  .social-links {
    gap: 12px;
  }
  
  .social-links a {
    width: 35px;
    height: 35px;
  }
  
  .social-links a svg {
    width: 16px;
    height: 16px;
  }
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .footer {
    background: #000;
    border-top: 2px solid #fff;
  }
  
  .footer-section h3,
  .footer-section h4 {
    color: #fff;
  }
  
  .footer-section p,
  .footer-section ul li a {
    color: #fff;
  }
  
  .social-links a {
    border: 2px solid #fff;
    background: #000;
  }
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
  .footer-section ul li a,
  .social-links a,
  .social-links a svg {
    transition: none;
  }
  
  .footer-section ul li a:hover,
  .social-links a:hover {
    transform: none;
  }
  
  .social-links a:hover svg {
    transform: none;
  }
} 