/* 
 * PLACE OF GRACE CHILDREN'S HOME - UNIFIED THEME VARIABLES
 * Production-Ready Design System with Enhanced Security & UX
 * Cross-Platform Compatible | Responsive | Accessible
 */

:root {
  /* === PRIMARY COLOR PALETTE === */
  /* Core Brand Colors */
  --primary-navy: #0f4c75;           /* Primary navy blue - trust, stability */
  --primary-blue: #1e3a8a;          /* Deep blue - reliability */
  --secondary-blue: #3b82f6;        /* Bright blue - hope, growth */
  --accent-gold: #FFD700;           /* Gold - warmth, care, premium */
  --light-blue: #bbe1fa;           /* Light blue - gentle, calming */
  --navy-dark: #001f4c;            /* Darker navy - depth, professionalism */
  
  /* Extended Color Palette */
  --blue-50: #eff6ff;
  --blue-100: #dbeafe;
  --blue-200: #bfdbfe;
  --blue-300: #93c5fd;
  --blue-400: #60a5fa;
  --blue-500: #3b82f6;
  --blue-600: #2563eb;
  --blue-700: #1d4ed8;
  --blue-800: #1e40af;
  --blue-900: #1e3a8a;
  
  /* Neutral Colors */
  --white: #ffffff;
  --gray-50: #f9fafb;
  --gray-100: #f3f4f6;
  --gray-200: #e5e7eb;
  --gray-300: #d1d5db;
  --gray-400: #9ca3af;
  --gray-500: #6b7280;
  --gray-600: #4b5563;
  --gray-700: #374151;
  --gray-800: #1f2937;
  --gray-900: #111827;
  
  /* Semantic Colors */
  --success: #10b981;
  --warning: #f59e0b;
  --error: #ef4444;
  --info: #3b82f6;
  
  /* Background Colors */
  --bg-primary: var(--white);
  --bg-secondary: var(--gray-50);
  --bg-tertiary: var(--gray-100);
  --bg-overlay: rgba(0, 0, 0, 0.5);
  --bg-overlay-light: rgba(255, 255, 255, 0.95);
  
  /* === TYPOGRAPHY SYSTEM === */
  /* Font Families - Cross-Platform Compatible */
  --font-primary: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Helvetica Neue', Arial, 'Noto Sans', sans-serif;
  --font-heading: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  --font-mono: 'SF Mono', 'Monaco', 'Cascadia Code', 'Roboto Mono', 'Consolas', 'Courier New', monospace;
  
  /* Font Weights */
  --font-light: 300;
  --font-normal: 400;
  --font-medium: 500;
  --font-semibold: 600;
  --font-bold: 700;
  --font-extrabold: 800;
  --font-black: 900;
  
  /* Font Sizes - Responsive Scale */
  --text-xs: clamp(0.7rem, 1.5vw, 0.75rem);      /* 11-12px */
  --text-sm: clamp(0.8rem, 1.8vw, 0.875rem);     /* 13-14px */
  --text-base: clamp(0.9rem, 2vw, 1rem);         /* 14-16px */
  --text-lg: clamp(1rem, 2.2vw, 1.125rem);       /* 16-18px */
  --text-xl: clamp(1.1rem, 2.5vw, 1.25rem);      /* 18-20px */
  --text-2xl: clamp(1.3rem, 3vw, 1.5rem);        /* 21-24px */
  --text-3xl: clamp(1.6rem, 4vw, 1.875rem);      /* 26-30px */
  --text-4xl: clamp(2rem, 5vw, 2.25rem);         /* 32-36px */
  --text-5xl: clamp(2.5rem, 6vw, 3rem);          /* 40-48px */
  --text-6xl: clamp(3rem, 8vw, 3.75rem);         /* 48-60px */
  --text-7xl: clamp(3.5rem, 10vw, 4.5rem);       /* 56-72px */
  --text-8xl: clamp(4rem, 12vw, 6rem);           /* 64-96px */
  
  /* Line Heights */
  --leading-none: 1;
  --leading-tight: 1.25;
  --leading-snug: 1.375;
  --leading-normal: 1.5;
  --leading-relaxed: 1.625;
  --leading-loose: 2;
  
  /* Letter Spacing */
  --tracking-tighter: -0.05em;
  --tracking-tight: -0.025em;
  --tracking-normal: 0;
  --tracking-wide: 0.025em;
  --tracking-wider: 0.05em;
  --tracking-widest: 0.1em;
  
  /* === SPACING SYSTEM === */
  /* Based on 8px grid system */
  --space-px: 1px;
  --space-0: 0;
  --space-1: 0.25rem;    /* 4px */
  --space-2: 0.5rem;     /* 8px */
  --space-3: 0.75rem;    /* 12px */
  --space-4: 1rem;       /* 16px */
  --space-5: 1.25rem;    /* 20px */
  --space-6: 1.5rem;     /* 24px */
  --space-8: 2rem;       /* 32px */
  --space-10: 2.5rem;    /* 40px */
  --space-12: 3rem;      /* 48px */
  --space-16: 4rem;      /* 64px */
  --space-20: 5rem;      /* 80px */
  --space-24: 6rem;      /* 96px */
  --space-32: 8rem;      /* 128px */
  --space-40: 10rem;     /* 160px */
  --space-48: 12rem;     /* 192px */
  --space-56: 14rem;     /* 224px */
  --space-64: 16rem;     /* 256px */
  
  /* === SIZING SYSTEM === */
  /* Width/Height utilities */
  --size-auto: auto;
  --size-full: 100%;
  --size-screen: 100vw;
  --size-min: min-content;
  --size-max: max-content;
  --size-fit: fit-content;
  
  /* Container Sizes */
  --container-sm: 640px;
  --container-md: 768px;
  --container-lg: 1024px;
  --container-xl: 1280px;
  --container-2xl: 1536px;
  --container-max: 1400px;
  
  /* === BORDER RADIUS === */
  --radius-none: 0;
  --radius-sm: 0.125rem;     /* 2px */
  --radius-base: 0.25rem;    /* 4px */
  --radius-md: 0.375rem;     /* 6px */
  --radius-lg: 0.5rem;       /* 8px */
  --radius-xl: 0.75rem;      /* 12px */
  --radius-2xl: 1rem;        /* 16px */
  --radius-3xl: 1.5rem;      /* 24px */
  --radius-full: 9999px;
  
  /* === SHADOWS === */
  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --shadow-base: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  --shadow-2xl: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  --shadow-inner: inset 0 2px 4px 0 rgba(0, 0, 0, 0.06);
  --shadow-none: none;
  
  /* Brand Specific Shadows */
  --shadow-brand: 0 10px 30px rgba(15, 76, 117, 0.15);
  --shadow-brand-lg: 0 20px 40px rgba(15, 76, 117, 0.2);
  --shadow-gold: 0 5px 15px rgba(255, 215, 0, 0.3);
  
  /* === TRANSITIONS === */
  --transition-fast: 150ms ease;
  --transition-base: 250ms ease;
  --transition-slow: 350ms ease;
  --transition-slower: 500ms ease;
  
  /* Specific Transitions */
  --transition-colors: color 150ms ease, background-color 150ms ease, border-color 150ms ease;
  --transition-opacity: opacity 150ms ease;
  --transition-shadow: box-shadow 150ms ease;
  --transition-transform: transform 150ms ease;
  --transition-all: all 150ms ease;
  
  /* === Z-INDEX SCALE === */
  --z-auto: auto;
  --z-0: 0;
  --z-10: 10;
  --z-20: 20;
  --z-30: 30;
  --z-40: 40;
  --z-50: 50;
  --z-dropdown: 1000;
  --z-sticky: 1020;
  --z-fixed: 1030;
  --z-modal-backdrop: 1040;
  --z-modal: 1050;
  --z-popover: 1060;
  --z-tooltip: 1070;
  --z-toast: 1080;
  
  /* === BREAKPOINTS === */
  --bp-xs: 320px;
  --bp-sm: 640px;
  --bp-md: 768px;
  --bp-lg: 1024px;
  --bp-xl: 1280px;
  --bp-2xl: 1536px;
  
  /* === ANIMATION DURATIONS === */
  --duration-75: 75ms;
  --duration-100: 100ms;
  --duration-150: 150ms;
  --duration-200: 200ms;
  --duration-300: 300ms;
  --duration-500: 500ms;
  --duration-700: 700ms;
  --duration-1000: 1000ms;
  
  /* === EASING FUNCTIONS === */
  --ease-linear: linear;
  --ease-in: cubic-bezier(0.4, 0, 1, 1);
  --ease-out: cubic-bezier(0, 0, 0.2, 1);
  --ease-in-out: cubic-bezier(0.4, 0, 0.2, 1);
  --ease-back: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  --ease-bounce: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  
  /* === COMPONENT SPECIFIC === */
  /* Button Variables */
  --btn-height-sm: 2rem;
  --btn-height-md: 2.5rem;
  --btn-height-lg: 3rem;
  --btn-height-xl: 3.5rem;
  
  /* Input Variables */
  --input-height-sm: 2rem;
  --input-height-md: 2.5rem;
  --input-height-lg: 3rem;
  
  /* Card Variables */
  --card-bg: var(--white);
  --card-border: var(--gray-200);
  --card-shadow: var(--shadow-md);
  --card-radius: var(--radius-xl);
  
  /* Navigation Variables */
  --nav-height: 80px;
  --nav-height-mobile: 70px;
  --nav-bg: rgba(255, 255, 255, 0.98);
  --nav-border: rgba(15, 76, 117, 0.1);
  
  /* Gallery Variables */
  --gallery-grid-gap: 2rem;
  --gallery-grid-gap-mobile: 1rem;
  --gallery-item-radius: var(--radius-2xl);
  --gallery-hover-scale: 1.05;
  
  /* Modal Variables */
  --modal-bg: rgba(0, 0, 0, 0.8);
  --modal-radius: var(--radius-3xl);
  --modal-shadow: var(--shadow-2xl);
  
  /* Loading Variables */
  --loading-spinner-size: 40px;
  --loading-spinner-border: 4px;
  --loading-bg: var(--bg-overlay-light);
}

/* === RESPONSIVE DESIGN TOKENS === */
@media (max-width: 768px) {
  :root {
    --nav-height: var(--nav-height-mobile);
    --gallery-grid-gap: var(--gallery-grid-gap-mobile);
    --container-padding: var(--space-4);
  }
}

@media (max-width: 480px) {
  :root {
    --container-padding: var(--space-3);
  }
}

/* === ACCESSIBILITY & MOTION === */
@media (prefers-reduced-motion: reduce) {
  :root {
    --transition-fast: none;
    --transition-base: none;
    --transition-slow: none;
    --transition-slower: none;
    --transition-all: none;
  }
}

@media (prefers-contrast: high) {
  :root {
    --shadow-brand: 0 0 0 2px var(--primary-navy);
    --shadow-brand-lg: 0 0 0 3px var(--primary-navy);
  }
}

/* === DARK MODE SUPPORT (Future Enhancement) === */
@media (prefers-color-scheme: dark) {
  :root {
    --bg-primary: var(--gray-900);
    --bg-secondary: var(--gray-800);
    --bg-tertiary: var(--gray-700);
    --card-bg: var(--gray-800);
    --card-border: var(--gray-600);
  }
}

/* === UTILITY CLASSES === */
.container {
  width: 100%;
  max-width: var(--container-max);
  margin: 0 auto;
  padding: 0 var(--space-5);
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.focus-visible {
  outline: 2px solid var(--primary-blue);
  outline-offset: 2px;
}

.transition-all {
  transition: var(--transition-all);
}

.hover-lift:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-brand);
}

.text-gradient {
  background: linear-gradient(135deg, var(--primary-navy) 0%, var(--secondary-blue) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
