.faq-page {
  min-height: 100vh;
}

.faq-page .hero-section {
  background: linear-gradient(135deg, #0f4c75 0%, #3282b8 25%, #bbe1fa 50%, #3282b8 75%, #0f4c75 100%);
  background-size: 400% 400%;
  animation: enhancedGradientShift 20s ease infinite;
  color: white;
  padding: 100px 0;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.faq-page .hero-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 80%, rgba(255, 255, 255, 0.15) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(255, 255, 255, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 40% 40%, rgba(255, 255, 255, 0.08) 0%, transparent 50%);
  animation: floatingLights 15s ease-in-out infinite;
  z-index: 1;
}

.faq-page .hero-section::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="hero-pattern" width="100" height="100" patternUnits="userSpaceOnUse"><circle cx="20" cy="20" r="1.5" fill="%23ffffff" opacity="0.2"/><circle cx="80" cy="80" r="1.5" fill="%23ffffff" opacity="0.2"/><circle cx="50" cy="10" r="1" fill="%23ffffff" opacity="0.25"/><circle cx="10" cy="60" r="1" fill="%23ffffff" opacity="0.25"/><circle cx="90" cy="40" r="1" fill="%23ffffff" opacity="0.25"/><circle cx="30" cy="90" r="2" fill="%23ffffff" opacity="0.15"/><circle cx="70" cy="30" r="2" fill="%23ffffff" opacity="0.15"/></pattern></defs><rect width="100" height="100" fill="url(%23hero-pattern)"/></svg>');
  opacity: 0.6;
  animation: patternFloat 25s ease-in-out infinite;
  z-index: 1;
}

.faq-page .hero-section h1 {
  font-size: clamp(3rem, 7vw, 5rem);
  margin-bottom: 25px;
  font-weight: 900;
  position: relative;
  z-index: 2;
  text-shadow: 
    0 4px 20px rgba(0, 0, 0, 0.4),
    0 2px 8px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(255, 255, 255, 0.1);
  background: linear-gradient(135deg, #ffffff 0%, #f0f8ff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3));
  animation: enhancedFadeInUp 1.2s cubic-bezier(0.4, 0, 0.2, 1) forwards;
}

.faq-page .hero-subtitle {
  font-size: clamp(1.3rem, 3.5vw, 1.8rem);
  opacity: 1;
  max-width: 700px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
  text-shadow: 
    0 3px 15px rgba(0, 0, 0, 0.4),
    0 1px 5px rgba(0, 0, 0, 0.3);
  font-weight: 500;
  color: #f8f9ff;
  animation: enhancedFadeInUp 1.2s cubic-bezier(0.4, 0, 0.2, 1) 0.4s both;
  position: relative;
}

.faq-page .hero-subtitle::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background: linear-gradient(90deg, transparent, #FFD700, transparent);
  border-radius: 2px;
  animation: shimmer 2s ease-in-out infinite;
}

@keyframes enhancedGradientShift {
  0%, 100% { 
    background-position: 0% 50%; 
    filter: hue-rotate(0deg);
  }
  25% { 
    background-position: 100% 50%; 
    filter: hue-rotate(5deg);
  }
  50% { 
    background-position: 100% 100%; 
    filter: hue-rotate(10deg);
  }
  75% { 
    background-position: 0% 100%; 
    filter: hue-rotate(5deg);
  }
}

@keyframes floatingLights {
  0%, 100% { 
    transform: translateY(0px) rotate(0deg);
    opacity: 0.8;
  }
  33% { 
    transform: translateY(-20px) rotate(1deg);
    opacity: 1;
  }
  66% { 
    transform: translateY(10px) rotate(-1deg);
    opacity: 0.9;
  }
}

@keyframes patternFloat {
  0%, 100% { 
    transform: translateY(0px) rotate(0deg) scale(1);
  }
  50% { 
    transform: translateY(-15px) rotate(0.5deg) scale(1.02);
  }
}

@keyframes enhancedFadeInUp {
  from {
    opacity: 0;
    transform: translateY(50px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

@keyframes shimmer {
  0%, 100% { 
    opacity: 0.5;
    transform: translateX(-50%) scaleX(0.8);
  }
  50% { 
    opacity: 1;
    transform: translateX(-50%) scaleX(1.2);
  }
}

.faq-content {
  padding: 60px 0;
}

.faq-navigation {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 3rem;
  flex-wrap: wrap;
}

.category-btn {
  padding: 12px 24px;
  border: 2px solid #1e3a8a;
  background: transparent;
  color: #1e3a8a;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
  font-size: 1rem;
}

.category-btn:hover {
  background: #1e3a8a;
  color: white;
  transform: translateY(-2px);
}

.category-btn.active {
  background: #1e3a8a;
  color: white;
}

.faq-list {
  max-width: 800px;
  margin: 0 auto;
}

.faq-item {
  margin-bottom: 1rem;
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  overflow: hidden;
}

.faq-item details {
  background: white;
}

.faq-item summary {
  padding: 1.5rem;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #f8fafc;
  transition: background-color 0.3s ease;
}

.faq-item summary:hover {
  background: #f1f5f9;
}

.faq-item summary h3 {
  font-size: 1.1rem;
  color: #1e3a8a;
  margin: 0;
  font-weight: 600;
  flex: 1;
  text-align: left;
}

.expand-icon {
  font-size: 1.5rem;
  color: #1e3a8a;
  font-weight: bold;
  transition: transform 0.3s ease;
}

.faq-item details[open] .expand-icon {
  transform: rotate(45deg);
}

.faq-answer {
  padding: 0 1.5rem 1.5rem;
  color: #475569;
  line-height: 1.7;
}

.faq-answer p {
  margin: 0;
  font-size: 1rem;
}

.contact-section {
  background: #f8fafc;
  padding: 60px 0;
  text-align: center;
}

.contact-section h2 {
  font-size: clamp(1.8rem, 4vw, 2.5rem);
  color: #1e3a8a;
  margin-bottom: 1rem;
}

.contact-section > .container > p {
  font-size: 1.1rem;
  color: #475569;
  max-width: 600px;
  margin: 0 auto 3rem;
  line-height: 1.7;
}

.contact-options {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.contact-option {
  background: white;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
}

.contact-option h3 {
  color: #1e3a8a;
  margin-bottom: 0.5rem;
  font-size: 1.2rem;
}

.contact-option p {
  color: #64748b;
  margin: 0;
  font-size: 1rem;
}

.contact-btn {
  display: inline-block;
  padding: 12px 24px;
  background: #1e3a8a;
  color: white;
  text-decoration: none;
  border-radius: 8px;
  font-weight: 600;
  transition: all 0.3s ease;
}

.contact-btn:hover {
  background: #1e40af;
  transform: translateY(-2px);
}

/* Responsive Design */
@media (max-width: 768px) {
  .faq-navigation {
    flex-direction: column;
    align-items: center;
  }

  .category-btn {
    width: 200px;
  }

  .faq-item summary {
    padding: 1rem;
  }

  .faq-item summary h3 {
    font-size: 1rem;
  }

  .faq-answer {
    padding: 0 1rem 1rem;
  }

  .contact-options {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .contact-option {
    padding: 1.5rem;
  }
}

@media (max-width: 480px) {
  .faq-item summary h3 {
    font-size: 0.9rem;
  }

  .faq-answer p {
    font-size: 0.9rem;
  }

  .expand-icon {
    font-size: 1.2rem;
  }
}

/* Accessibility */
@media (prefers-reduced-motion: reduce) {
  .category-btn,
  .faq-item summary,
  .expand-icon,
  .contact-btn {
    transition: none;
  }
}

@media (prefers-contrast: high) {
  .faq-item {
    border-color: #000;
  }

  .faq-item summary h3 {
    color: #000;
  }

  .faq-answer {
    color: #333;
  }
}

/* Focus styles for keyboard navigation */
.category-btn:focus,
.faq-item summary:focus,
.contact-btn:focus {
  outline: 2px solid #3b82f6;
  outline-offset: 2px;
} 