/* Get Involved Page Styles */
.get-involved-page {
  min-height: 100vh;
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
}

.get-involved-page .container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Hero Section - Enhanced for better visibility and UX */
.get-involved-page .hero-section {
  background: linear-gradient(135deg, #0f4c75 0%, #3282b8 25%, #bbe1fa 50%, #3282b8 75%, #0f4c75 100%);
  background-size: 400% 400%;
  animation: enhancedGradientShift 20s ease infinite;
  color: white;
  text-align: center;
  padding: 100px 0;
  position: relative;
  overflow: hidden;
}

.get-involved-page .hero-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 80%, rgba(255, 255, 255, 0.15) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(255, 255, 255, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 40% 40%, rgba(255, 255, 255, 0.08) 0%, transparent 50%);
  animation: floatingLights 15s ease-in-out infinite;
  z-index: 1;
}

.get-involved-page .hero-section::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grain" width="100" height="100" patternUnits="userSpaceOnUse"><circle cx="25" cy="25" r="1.5" fill="white" opacity="0.2"/><circle cx="75" cy="75" r="1.5" fill="white" opacity="0.2"/><circle cx="50" cy="10" r="1" fill="white" opacity="0.25"/><circle cx="10" cy="60" r="1" fill="white" opacity="0.25"/><circle cx="90" cy="40" r="1" fill="white" opacity="0.25"/><circle cx="30" cy="90" r="2" fill="white" opacity="0.15"/><circle cx="70" cy="30" r="2" fill="white" opacity="0.15"/></pattern></defs><rect width="100" height="100" fill="url(%23grain)"/></svg>');
  opacity: 0.6;
  animation: patternFloat 25s ease-in-out infinite;
  z-index: 1;
}

.get-involved-page .hero-section h1 {
  font-size: clamp(3rem, 7vw, 5rem);
  font-weight: 900;
  margin-bottom: 25px;
  position: relative;
  z-index: 2;
  text-shadow: 
    0 4px 20px rgba(0, 0, 0, 0.4),
    0 2px 8px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(255, 255, 255, 0.1);
  background: linear-gradient(135deg, #ffffff 0%, #f0f8ff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3));
  animation: enhancedFadeInUp 1.2s cubic-bezier(0.4, 0, 0.2, 1) forwards;
}

.get-involved-page .hero-subtitle {
  font-size: clamp(1.3rem, 3.5vw, 1.8rem);
  max-width: 700px;
  margin: 0 auto;
  opacity: 1;
  position: relative;
  z-index: 2;
  text-shadow: 
    0 3px 15px rgba(0, 0, 0, 0.4),
    0 1px 5px rgba(0, 0, 0, 0.3);
  font-weight: 500;
  color: #f8f9ff;
  animation: enhancedFadeInUp 1.2s cubic-bezier(0.4, 0, 0.2, 1) 0.4s both;
  position: relative;
}

.get-involved-page .hero-subtitle::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background: linear-gradient(90deg, transparent, #FFD700, transparent);
  border-radius: 2px;
  animation: shimmer 2s ease-in-out infinite;
}

@keyframes enhancedGradientShift {
  0%, 100% { 
    background-position: 0% 50%; 
    filter: hue-rotate(0deg);
  }
  25% { 
    background-position: 100% 50%; 
    filter: hue-rotate(5deg);
  }
  50% { 
    background-position: 100% 100%; 
    filter: hue-rotate(10deg);
  }
  75% { 
    background-position: 0% 100%; 
    filter: hue-rotate(5deg);
  }
}

@keyframes floatingLights {
  0%, 100% { 
    transform: translateY(0px) rotate(0deg);
    opacity: 0.8;
  }
  33% { 
    transform: translateY(-20px) rotate(1deg);
    opacity: 1;
  }
  66% { 
    transform: translateY(10px) rotate(-1deg);
    opacity: 0.9;
  }
}

@keyframes patternFloat {
  0%, 100% { 
    transform: translateY(0px) rotate(0deg) scale(1);
  }
  50% { 
    transform: translateY(-15px) rotate(0.5deg) scale(1.02);
  }
}

@keyframes enhancedFadeInUp {
  from {
    opacity: 0;
    transform: translateY(50px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

@keyframes shimmer {
  0%, 100% { 
    opacity: 0.5;
    transform: translateX(-50%) scaleX(0.8);
  }
  50% { 
    opacity: 1;
    transform: translateX(-50%) scaleX(1.2);
  }
}

/* Section Styles */
.get-involved-page section {
  padding: 80px 0;
}

.get-involved-page section:nth-child(even) {
  background: white;
}

.get-involved-page section:nth-child(odd) {
  background: #f8fafc;
}

.get-involved-page h2 {
  font-size: clamp(2rem, 4vw, 3rem);
  font-weight: 600;
  text-align: center;
  margin-bottom: 20px;
  color: #1e3a8a;
}

.get-involved-page .section-intro {
  font-size: 1.2rem;
  text-align: center;
  max-width: 700px;
  margin: 0 auto 60px;
  color: #64748b;
  line-height: 1.6;
}

/* Donate Section */
.donation-options-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  margin-bottom: 60px;
}

.donation-option-card {
  background: white;
  border-radius: 20px;
  padding: 40px 30px;
  text-align: center;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  border: 2px solid transparent;
}

.donation-option-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
  border-color: #3b82f6;
}

.option-icon {
  font-size: 3rem;
  margin-bottom: 20px;
}

.donation-option-card h3 {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 15px;
  color: #1e3a8a;
}

.donation-option-card p {
  color: #64748b;
  margin-bottom: 20px;
  line-height: 1.6;
}

.amount {
  font-size: 1.2rem;
  font-weight: 600;
  color: #059669;
  background: #d1fae5;
  padding: 10px 20px;
  border-radius: 25px;
  display: inline-block;
}

/* Donation Form */
.donation-form-container {
  background: white;
  border-radius: 20px;
  padding: 40px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  margin: 0 auto;
}

.donation-form-container h3 {
  font-size: 1.8rem;
  font-weight: 600;
  text-align: center;
  margin-bottom: 30px;
  color: #1e3a8a;
}

.donation-form .form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin-bottom: 30px;
}

.donation-form .form-group {
  display: flex;
  flex-direction: column;
}

.donation-form label {
  font-weight: 500;
  margin-bottom: 8px;
  color: #374151;
}

.donation-form select,
.donation-form input {
  padding: 15px;
  border: 2px solid #e5e7eb;
  border-radius: 10px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

.donation-form select:focus,
.donation-form input:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.donate-button {
  width: 100%;
  background: linear-gradient(135deg, #059669 0%, #10b981 100%);
  color: white;
  border: none;
  padding: 18px;
  border-radius: 10px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.donate-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(5, 150, 105, 0.3);
}

/* Volunteer Section */
.volunteer-opportunities-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
  margin-bottom: 60px;
}

.volunteer-opportunity-card {
  background: white;
  border-radius: 20px;
  padding: 30px;
  text-align: center;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  border: 2px solid transparent;
}

.volunteer-opportunity-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
  border-color: #f59e0b;
}

.opportunity-icon {
  font-size: 3rem;
  margin-bottom: 20px;
}

.volunteer-opportunity-card h3 {
  font-size: 1.3rem;
  font-weight: 600;
  margin-bottom: 15px;
  color: #1e3a8a;
}

.volunteer-opportunity-card p {
  color: #64748b;
  margin-bottom: 20px;
  line-height: 1.6;
}

.commitment {
  background: #fef3c7;
  color: #92400e;
  padding: 10px 15px;
  border-radius: 10px;
  margin-bottom: 20px;
  font-size: 0.9rem;
}

.volunteer-button {
  background: linear-gradient(135deg, #f59e0b 0%, #fbbf24 100%);
  color: white;
  border: none;
  padding: 12px 25px;
  border-radius: 25px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.volunteer-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(245, 158, 11, 0.3);
}

.volunteer-cta {
  text-align: center;
  background: linear-gradient(135deg, #f59e0b 0%, #fbbf24 100%);
  color: white;
  padding: 40px;
  border-radius: 20px;
  margin-top: 40px;
}

.volunteer-cta h3 {
  font-size: 1.8rem;
  margin-bottom: 15px;
}

.volunteer-cta p {
  margin-bottom: 25px;
  opacity: 0.95;
}

/* Corporate Partnerships Section */
.partnership-benefits {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 40px;
  margin-bottom: 60px;
}

.benefit-card,
.partnership-areas {
  background: white;
  border-radius: 20px;
  padding: 40px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.benefit-card h3,
.partnership-areas h3 {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 25px;
  color: #1e3a8a;
}

.benefit-card ul,
.partnership-areas ul {
  list-style: none;
  padding: 0;
}

.benefit-card li,
.partnership-areas li {
  padding: 12px 0;
  border-bottom: 1px solid #f1f5f9;
  color: #64748b;
  position: relative;
  padding-left: 25px;
}

.benefit-card li:before,
.partnership-areas li:before {
  content: '✓';
  position: absolute;
  left: 0;
  color: #059669;
  font-weight: bold;
}

.benefit-card li:last-child,
.partnership-areas li:last-child {
  border-bottom: none;
}

.partnership-cta {
  text-align: center;
  background: linear-gradient(135deg, #1e3a8a 0%, #3b82f6 100%);
  color: white;
  padding: 40px;
  border-radius: 20px;
}

.partnership-cta h3 {
  font-size: 1.8rem;
  margin-bottom: 15px;
}

.partnership-cta p {
  margin-bottom: 25px;
  opacity: 0.95;
}

/* Contact & Donation Info Section */
.contact-donations-info {
  background: white;
}

.contact-info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
  margin-bottom: 40px;
}

.contact-item {
  text-align: center;
  padding: 20px;
  background: #f8fafc;
  border-radius: 15px;
}

.contact-item h4 {
  font-size: 1.1rem;
  font-weight: 600;
  margin-bottom: 10px;
  color: #1e3a8a;
}

.contact-item a {
  color: #3b82f6;
  text-decoration: none;
  font-weight: 500;
}

.contact-item a:hover {
  text-decoration: underline;
}

.donation-methods-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
}

.donation-method-card {
  background: #f8fafc;
  border-radius: 15px;
  padding: 30px;
  border: 2px solid #e2e8f0;
}

.donation-method-card h4 {
  font-size: 1.3rem;
  font-weight: 600;
  margin-bottom: 20px;
  color: #1e3a8a;
}

.donation-method-card p {
  margin-bottom: 10px;
  color: #64748b;
}

.donation-method-card strong {
  color: #374151;
}

/* CTA Buttons */
.cta-button {
  display: inline-block;
  background: white;
  color: #1e3a8a;
  padding: 15px 30px;
  border-radius: 25px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  border: 2px solid white;
}

.cta-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
}

.cta-button.primary {
  background: #1e3a8a;
  color: white;
}

/* Responsive Design */
@media (max-width: 768px) {
  .get-involved-page .hero-section {
    padding: 60px 0;
  }
  
  .get-involved-page section {
    padding: 60px 0;
  }
  
  .donation-form .form-row {
    grid-template-columns: 1fr;
  }
  
  .partnership-benefits {
    grid-template-columns: 1fr;
  }
  
  .donation-options-grid,
  .volunteer-opportunities-grid {
    grid-template-columns: 1fr;
  }
  
  .donation-form-container,
  .benefit-card,
  .partnership-areas {
    padding: 30px 20px;
  }
}

@media (max-width: 480px) {
  .get-involved-page .hero-section {
    padding: 40px 0;
  }
  
  .get-involved-page section {
    padding: 40px 0;
  }
  
  .donation-form-container,
  .benefit-card,
  .partnership-areas {
    padding: 20px 15px;
  }
  
  .volunteer-cta,
  .partnership-cta {
    padding: 30px 20px;
  }
} 