/* HomePage Styles */
.home-page {
  min-height: 100vh;
}

/* Hero Welcome Section - Enhanced for better visibility and UX */
.hero-welcome-section {
  min-height: 100vh;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  background: linear-gradient(135deg, #0f4c75 0%, #3282b8 25%, #bbe1fa 50%, #3282b8 75%, #0f4c75 100%);
  background-size: 400% 400%;
  animation: enhancedGradientShift 20s ease infinite;
}

.hero-welcome-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 80%, rgba(255, 255, 255, 0.15) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(255, 255, 255, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 40% 40%, rgba(255, 255, 255, 0.08) 0%, transparent 50%);
  animation: floatingLights 15s ease-in-out infinite;
  z-index: 1;
}

.hero-welcome-section::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="hero-pattern" width="100" height="100" patternUnits="userSpaceOnUse"><circle cx="20" cy="20" r="1.5" fill="%23ffffff" opacity="0.2"/><circle cx="80" cy="80" r="1.5" fill="%23ffffff" opacity="0.2"/><circle cx="50" cy="10" r="1" fill="%23ffffff" opacity="0.25"/><circle cx="10" cy="60" r="1" fill="%23ffffff" opacity="0.25"/><circle cx="90" cy="40" r="1" fill="%23ffffff" opacity="0.25"/><circle cx="30" cy="90" r="2" fill="%23ffffff" opacity="0.15"/><circle cx="70" cy="30" r="2" fill="%23ffffff" opacity="0.15"/></pattern></defs><rect width="100" height="100" fill="url(%23hero-pattern)"/></svg>');
  opacity: 0.6;
  animation: patternFloat 25s ease-in-out infinite;
  z-index: 1;
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, #0f4c75 0%, #3282b8 25%, #bbe1fa 50%, #3282b8 75%, #0f4c75 100%);
  background-size: 400% 400%;
  animation: enhancedGradientShift 20s ease infinite;
}

.hero-background::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="hero-pattern" width="100" height="100" patternUnits="userSpaceOnUse"><circle cx="20" cy="20" r="2" fill="%23ffffff" opacity="0.1"/><circle cx="80" cy="80" r="2" fill="%23ffffff" opacity="0.1"/><circle cx="50" cy="10" r="1" fill="%23ffffff" opacity="0.15"/><circle cx="10" cy="60" r="1" fill="%23ffffff" opacity="0.15"/><circle cx="90" cy="40" r="1" fill="%23ffffff" opacity="0.15"/><circle cx="30" cy="90" r="1.5" fill="%23ffffff" opacity="0.1"/><circle cx="70" cy="30" r="1.5" fill="%23ffffff" opacity="0.1"/></pattern></defs><rect width="100" height="100" fill="url(%23hero-pattern)"/></svg>');
  opacity: 0.4;
  animation: float 20s ease-in-out infinite;
}

.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, 
    rgba(15, 76, 117, 0.3) 0%, 
    rgba(50, 130, 184, 0.2) 25%, 
    rgba(187, 225, 250, 0.1) 50%, 
    rgba(50, 130, 184, 0.2) 75%, 
    rgba(15, 76, 117, 0.3) 100%);
  z-index: 2;
}

@keyframes enhancedGradientShift {
  0%, 100% { 
    background-position: 0% 50%; 
    filter: hue-rotate(0deg);
  }
  25% { 
    background-position: 100% 50%; 
    filter: hue-rotate(5deg);
  }
  50% { 
    background-position: 100% 100%; 
    filter: hue-rotate(10deg);
  }
  75% { 
    background-position: 0% 100%; 
    filter: hue-rotate(5deg);
  }
}

@keyframes floatingLights {
  0%, 100% { 
    transform: translateY(0px) rotate(0deg);
    opacity: 0.8;
  }
  33% { 
    transform: translateY(-20px) rotate(1deg);
    opacity: 1;
  }
  66% { 
    transform: translateY(10px) rotate(-1deg);
    opacity: 0.9;
  }
}

@keyframes patternFloat {
  0%, 100% { 
    transform: translateY(0px) rotate(0deg) scale(1);
  }
  50% { 
    transform: translateY(-15px) rotate(0.5deg) scale(1.02);
  }
}

@keyframes float {
  0%, 100% { transform: translateY(0px) rotate(0deg); }
  50% { transform: translateY(-15px) rotate(1deg); }
}

.hero-welcome-content {
  text-align: center;
  max-width: 900px;
  margin: 0 auto;
  position: relative;
  z-index: 3;
  padding: 0 20px;
}

.hero-welcome-title {
  font-size: clamp(3.5rem, 8vw, 6rem);
  font-weight: 900;
  color: white;
  margin-bottom: 30px;
  opacity: 0;
  transform: translateY(50px) scale(0.95);
  animation: enhancedSlideInUp 1.2s cubic-bezier(0.4, 0, 0.2, 1) 0.3s forwards;
  text-shadow: 
    0 4px 20px rgba(0, 0, 0, 0.4),
    0 2px 8px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(255, 255, 255, 0.1);
  letter-spacing: -2px;
  line-height: 1.1;
  background: linear-gradient(135deg, #ffffff 0%, #f0f8ff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3));
}

.hero-welcome-subtitle {
  font-size: clamp(1.3rem, 3.5vw, 1.8rem);
  color: #f8f9ff;
  line-height: 1.6;
  margin-bottom: 50px;
  opacity: 0;
  transform: translateY(50px) scale(0.95);
  animation: enhancedSlideInUp 1.2s cubic-bezier(0.4, 0, 0.2, 1) 0.6s forwards;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
  text-shadow: 
    0 3px 15px rgba(0, 0, 0, 0.4),
    0 1px 5px rgba(0, 0, 0, 0.3);
  font-weight: 500;
  position: relative;
}

.hero-welcome-subtitle::after {
  content: '';
  position: absolute;
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 4px;
  background: linear-gradient(90deg, transparent, #FFD700, transparent);
  border-radius: 2px;
  animation: shimmer 2s ease-in-out infinite;
}

.hero-welcome-actions {
  display: flex;
  gap: 25px;
  justify-content: center;
  flex-wrap: wrap;
  opacity: 0;
  transform: translateY(50px) scale(0.95);
  animation: enhancedSlideInUp 1.2s cubic-bezier(0.4, 0, 0.2, 1) 0.9s forwards;
}

.hero-welcome-btn {
  display: inline-block;
  padding: 18px 36px;
  border-radius: 50px;
  font-weight: 700;
  font-size: 1.2rem;
  text-decoration: none;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  min-width: 200px;
  text-align: center;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
}

.hero-welcome-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.6s ease;
}

.hero-welcome-btn:hover::before {
  left: 100%;
}

.hero-welcome-btn.primary {
  background: linear-gradient(135deg, #059669 0%, #10b981 100%);
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.2);
}

.hero-welcome-btn.primary:hover {
  transform: translateY(-4px) scale(1.05);
  box-shadow: 0 15px 40px rgba(5, 150, 105, 0.4);
  background: linear-gradient(135deg, #047857 0%, #059669 100%);
  border-color: rgba(255, 255, 255, 0.4);
}

.hero-welcome-btn.secondary {
  background: rgba(255, 255, 255, 0.15);
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(10px);
}

.hero-welcome-btn.secondary:hover {
  transform: translateY(-4px) scale(1.05);
  box-shadow: 0 15px 40px rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.25);
  border-color: rgba(255, 255, 255, 0.5);
}

@keyframes enhancedSlideInUp {
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

@keyframes shimmer {
  0%, 100% { 
    opacity: 0.5;
    transform: translateX(-50%) scaleX(0.8);
  }
  50% { 
    opacity: 1;
    transform: translateX(-50%) scaleX(1.2);
  }
}

/* Mission Vision Section */
.mission-vision-section {
  padding: 80px 0;
  background: white;
}

.mission-vision-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 40px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.mission-card,
.vision-card {
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
  padding: 40px 30px;
  border-radius: 20px;
  text-align: center;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  border: 2px solid transparent;
  min-height: 250px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.mission-card:hover,
.vision-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
  border-color: #0f4c75;
}

.mission-card h2,
.vision-card h2 {
  font-size: 1.8rem;
  font-weight: 700;
  color: #0f4c75;
  margin-bottom: 20px;
}

.mission-card p,
.vision-card p {
  font-size: 1.1rem;
  color: #4b5563;
  line-height: 1.7;
}

/* Featured Story Section */
.featured-story-section {
  padding: 80px 0 120px 0;
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
  margin-bottom: 80px;
  position: relative;
  z-index: 1;
  min-height: 300px;
  display: block !important;
  visibility: visible !important;
}

.story-content {
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
  padding: 0 20px;
  position: relative;
  z-index: 2;
  display: block !important;
  visibility: visible !important;
}

.story-text h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: #1e3a8a;
  margin-bottom: 20px;
  display: block !important;
  visibility: visible !important;
}

.story-text p {
  font-size: 1.2rem;
  color: #4b5563;
  line-height: 1.7;
  margin-bottom: 30px;
}

.story-text {
  display: flex !important;
  flex-direction: column !important;
  align-items: center !important;
  gap: 20px !important;
  visibility: visible !important;
}

.read-more-btn {
  display: inline-block !important;
  background: linear-gradient(135deg, #3b82f6 0%, #1e3a8a 100%);
  color: white;
  padding: 16px 32px;
  border-radius: 25px;
  text-decoration: none;
  font-weight: 600;
  font-size: 1.2rem;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(59, 130, 246, 0.3);
  margin-top: 20px;
  min-width: 200px;
  position: relative;
  z-index: 10;
  opacity: 1 !important;
  visibility: visible !important;
  transform: none !important;
  top: auto !important;
  left: auto !important;
  right: auto !important;
  bottom: auto !important;
}

.read-more-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(59, 130, 246, 0.4);
  background: linear-gradient(135deg, #2563eb 0%, #1e40af 100%);
  display: inline-block !important;
  visibility: visible !important;
  opacity: 1 !important;
}

/* Force visibility for read-more-btn */
.home-page .featured-story-section .story-content .story-text .read-more-btn {
  display: inline-block !important;
  visibility: visible !important;
  opacity: 1 !important;
  position: relative !important;
  z-index: 100 !important;
}

/* Large Screen Styles */
@media (min-width: 769px) {
  .read-more-btn {
    display: inline-block !important;
    visibility: visible !important;
    opacity: 1 !important;
    font-size: 1.2rem;
    padding: 16px 32px;
    min-width: 200px;
  }
  
  .featured-story-section {
    display: block !important;
    visibility: visible !important;
    min-height: 300px;
  }
  
  .story-content {
    display: block !important;
    visibility: visible !important;
  }
}

/* Extra Large Screen Styles */
@media (min-width: 1200px) {
  .read-more-btn {
    display: inline-block !important;
    visibility: visible !important;
    opacity: 1 !important;
    font-size: 1.3rem;
    padding: 18px 36px;
    min-width: 220px;
  }
  
  .featured-story-section {
    display: block !important;
    visibility: visible !important;
    min-height: 350px;
    padding: 100px 0 140px 0;
  }
  
  .story-content {
    display: block !important;
    visibility: visible !important;
    max-width: 900px;
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .hero-welcome-section {
    min-height: 90vh;
    padding: 60px 0;
  }
  
  .hero-welcome-title {
    font-size: clamp(2.5rem, 10vw, 3.5rem);
  }
  
  .hero-welcome-subtitle {
    font-size: clamp(1.1rem, 4vw, 1.4rem);
  }
  
  .hero-welcome-actions {
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }
  
  .hero-welcome-btn {
    width: 100%;
    max-width: 300px;
    padding: 16px 32px;
  }
  
  .mission-vision-grid {
    grid-template-columns: 1fr;
    gap: 30px;
    padding: 0 15px;
  }
  
  .mission-card,
  .vision-card {
    padding: 30px 20px;
    min-height: auto;
  }
  
  .story-text h2 {
    font-size: 2rem;
  }
  
  .story-text p {
    font-size: 1.1rem;
  }
  
  .read-more-btn {
    font-size: 1rem;
    padding: 12px 24px;
    min-width: 180px;
  }
}

@media (max-width: 480px) {
  .hero-welcome-section {
    min-height: 80vh;
    padding: 40px 0;
  }
  
  .hero-welcome-title {
    font-size: 2.2rem;
  }
  
  .hero-welcome-subtitle {
    font-size: 1.1rem;
  }
  
  .hero-welcome-btn {
    padding: 14px 24px;
    font-size: 1.1rem;
  }
  
  .mission-vision-grid {
    padding: 0 10px;
  }
  
  .mission-card,
  .vision-card {
    padding: 25px 15px;
  }
  
  .mission-card h2,
  .vision-card h2 {
    font-size: 1.6rem;
  }
  
  .mission-card p,
  .vision-card p {
    font-size: 1rem;
  }
  
  .story-text h2 {
    font-size: 1.8rem;
  }
  
  .story-text p {
    font-size: 1rem;
  }
  
  .read-more-btn {
    font-size: 0.95rem;
    padding: 10px 20px;
    min-width: 160px;
  }
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .hero-welcome-section {
    background: #000;
  }
  
  .hero-welcome-title {
    color: #fff;
    text-shadow: 2px 2px 4px #000;
  }
  
  .hero-welcome-subtitle {
    color: #fff;
    text-shadow: 1px 1px 2px #000;
  }
  
  .hero-welcome-btn {
    border: 2px solid currentColor;
  }
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
  .hero-background,
  .hero-background::before,
  .hero-welcome-title,
  .hero-welcome-subtitle,
  .hero-welcome-actions,
  .hero-welcome-btn,
  .mission-card,
  .vision-card {
    animation: none;
    transition: none;
  }
  
  .hero-welcome-btn:hover,
  .mission-card:hover,
  .vision-card:hover {
    transform: none;
  }
  
  .read-more-btn {
    animation: none;
    transition: none;
    display: inline-block !important;
    visibility: visible !important;
    opacity: 1 !important;
  }
  
  .read-more-btn:hover {
    transform: none;
    display: inline-block !important;
    visibility: visible !important;
    opacity: 1 !important;
  }
} 