.programs-page {
  min-height: 100vh;
}

.programs-page .hero-section {
  background: linear-gradient(135deg, #0f4c75 0%, #3282b8 25%, #bbe1fa 50%, #3282b8 75%, #0f4c75 100%);
  background-size: 400% 400%;
  animation: enhancedGradientShift 20s ease infinite;
  color: white;
  padding: 100px 0;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.programs-page .hero-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 80%, rgba(255, 255, 255, 0.15) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(255, 255, 255, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 40% 40%, rgba(255, 255, 255, 0.08) 0%, transparent 50%);
  animation: floatingLights 15s ease-in-out infinite;
  z-index: 1;
}

.programs-page .hero-section::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="hero-pattern" width="100" height="100" patternUnits="userSpaceOnUse"><circle cx="20" cy="20" r="1.5" fill="%23ffffff" opacity="0.2"/><circle cx="80" cy="80" r="1.5" fill="%23ffffff" opacity="0.2"/><circle cx="50" cy="10" r="1" fill="%23ffffff" opacity="0.25"/><circle cx="10" cy="60" r="1" fill="%23ffffff" opacity="0.25"/><circle cx="90" cy="40" r="1" fill="%23ffffff" opacity="0.25"/><circle cx="30" cy="90" r="2" fill="%23ffffff" opacity="0.15"/><circle cx="70" cy="30" r="2" fill="%23ffffff" opacity="0.15"/></pattern></defs><rect width="100" height="100" fill="url(%23hero-pattern)"/></svg>');
  opacity: 0.6;
  animation: patternFloat 25s ease-in-out infinite;
  z-index: 1;
}

.programs-page .hero-section h1 {
  font-size: clamp(3rem, 7vw, 5rem);
  margin-bottom: 25px;
  font-weight: 900;
  position: relative;
  z-index: 2;
  text-shadow: 
    0 4px 20px rgba(0, 0, 0, 0.4),
    0 2px 8px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(255, 255, 255, 0.1);
  background: linear-gradient(135deg, #ffffff 0%, #f0f8ff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3));
  animation: enhancedFadeInUp 1.2s cubic-bezier(0.4, 0, 0.2, 1) forwards;
}

.programs-page .hero-subtitle {
  font-size: clamp(1.3rem, 3.5vw, 1.8rem);
  opacity: 1;
  max-width: 700px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
  text-shadow: 
    0 3px 15px rgba(0, 0, 0, 0.4),
    0 1px 5px rgba(0, 0, 0, 0.3);
  font-weight: 500;
  color: #f8f9ff;
  animation: enhancedFadeInUp 1.2s cubic-bezier(0.4, 0, 0.2, 1) 0.4s both;
  position: relative;
}

.programs-page .hero-subtitle::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background: linear-gradient(90deg, transparent, #FFD700, transparent);
  border-radius: 2px;
  animation: shimmer 2s ease-in-out infinite;
}

@keyframes enhancedGradientShift {
  0%, 100% { 
    background-position: 0% 50%; 
    filter: hue-rotate(0deg);
  }
  25% { 
    background-position: 100% 50%; 
    filter: hue-rotate(5deg);
  }
  50% { 
    background-position: 100% 100%; 
    filter: hue-rotate(10deg);
  }
  75% { 
    background-position: 0% 100%; 
    filter: hue-rotate(5deg);
  }
}

@keyframes floatingLights {
  0%, 100% { 
    transform: translateY(0px) rotate(0deg);
    opacity: 0.8;
  }
  33% { 
    transform: translateY(-20px) rotate(1deg);
    opacity: 1;
  }
  66% { 
    transform: translateY(10px) rotate(-1deg);
    opacity: 0.9;
  }
}

@keyframes patternFloat {
  0%, 100% { 
    transform: translateY(0px) rotate(0deg) scale(1);
  }
  50% { 
    transform: translateY(-15px) rotate(0.5deg) scale(1.02);
  }
}

@keyframes enhancedFadeInUp {
  from {
    opacity: 0;
    transform: translateY(50px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

@keyframes shimmer {
  0%, 100% { 
    opacity: 0.5;
    transform: translateX(-50%) scaleX(0.8);
  }
  50% { 
    opacity: 1;
    transform: translateX(-50%) scaleX(1.2);
  }
}

.programs-overview {
  padding: 60px 0;
  background-color: #f8fafc;
}

.overview-content {
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
}

.overview-content h2 {
  font-size: clamp(1.8rem, 4vw, 2.5rem);
  color: #0f4c75;
  margin-bottom: 1.5rem;
}

.overview-content p {
  font-size: 1.1rem;
  line-height: 1.7;
  color: #475569;
}

.programs-list {
  padding: 60px 0;
}

.program-item {
  margin-bottom: 80px;
}

.program-item:last-child {
  margin-bottom: 0;
}

.program-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
  align-items: center;
}

.program-item.right .program-content {
  direction: rtl;
}

.program-item.right .program-text {
  direction: ltr;
}

.program-text h3 {
  font-size: clamp(1.5rem, 3vw, 2rem);
  color: #0f4c75;
  margin-bottom: 1rem;
  font-weight: 600;
}

.program-text p {
  font-size: 1.1rem;
  line-height: 1.7;
  color: #475569;
  margin-bottom: 1.5rem;
}

.program-features {
  list-style: none;
  padding: 0;
}

.program-features li {
  padding: 0.5rem 0;
  padding-left: 1.5rem;
  position: relative;
  color: #64748b;
  font-size: 1rem;
}

.program-features li::before {
  content: "✓";
  position: absolute;
  left: 0;
  color: #10b981;
  font-weight: bold;
}

.program-image {
  position: relative;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.program-image img {
  width: 100%;
  height: 300px;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.program-image:hover img {
  transform: scale(1.05);
}

.image-placeholder {
  width: 100%;
  height: 300px;
  background: linear-gradient(135deg, #f1f5f9 0%, #e2e8f0 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
  border: 2px dashed #cbd5e1;
  color: #64748b;
}

.image-placeholder span {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.image-placeholder p {
  font-size: 1.1rem;
  font-weight: 500;
  margin: 0;
}

.impact-stats {
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
  padding: 80px 0;
  margin: 60px 0;
}

.impact-stats h2 {
  text-align: center;
  font-size: clamp(1.8rem, 4vw, 2.5rem);
  color: #0f4c75;
  margin-bottom: 3rem;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.stat-item {
  background: white;
  padding: 2.5rem;
  border-radius: 15px;
  text-align: center;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  cursor: pointer;
  border-top: 4px solid #3b82f6;
  position: relative;
  overflow: hidden;
}

.stat-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #3b82f6, #1e3a8a);
}

.stat-item:hover {
  transform: translateY(-8px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.15);
}

.stat-item h3 {
  font-size: clamp(2.5rem, 5vw, 3.5rem);
  font-weight: 700;
  color: #0f4c75;
  margin-bottom: 1rem;
  transition: all 0.3s ease;
}

.counting-number {
  background: linear-gradient(135deg, #0f4c75, #3b82f6);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.stat-item:hover .counting-number {
  transform: scale(1.1);
}

.stat-item p {
  font-size: 1.1rem;
  color: #64748b;
  font-weight: 500;
  line-height: 1.5;
}

/* Counting Animation */
@keyframes countUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.counting-number {
  animation: countUp 0.5s ease-out;
}

/* Pulse effect for clickable stats */
.stat-item:active {
  transform: translateY(-4px) scale(0.98);
}

.stat-item:active .counting-number {
  transform: scale(1.05);
}

.get-involved-cta {
  padding: 60px 0;
  background-color: #f8fafc;
  text-align: center;
}

.get-involved-cta h2 {
  font-size: clamp(1.8rem, 4vw, 2.5rem);
  color: #1e3a8a;
  margin-bottom: 1rem;
}

.get-involved-cta p {
  font-size: 1.1rem;
  color: #475569;
  max-width: 600px;
  margin: 0 auto 2rem;
  line-height: 1.7;
}

.cta-buttons {
  display: flex;
  gap: 1rem;
  justify-content: center;
  flex-wrap: wrap;
}

.cta-button {
  padding: 12px 24px;
  border-radius: 8px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  display: inline-block;
}

.cta-button.primary {
  background-color: #0f4c75;
  color: white;
}

.cta-button.primary:hover {
  background-color: #1e3a8a;
  transform: translateY(-2px);
}

.cta-button.secondary {
  background-color: transparent;
  color: #0f4c75;
  border: 2px solid #0f4c75;
}

.cta-button.secondary:hover {
  background-color: #0f4c75;
  color: white;
  transform: translateY(-2px);
}

/* Responsive Design */
@media (max-width: 768px) {
  .program-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .program-item.right .program-content {
    direction: ltr;
  }

  .program-image img {
    height: 250px;
  }

  .image-placeholder {
    height: 250px;
  }

  .image-placeholder span {
    font-size: 2.5rem;
  }

  .image-placeholder p {
    font-size: 1rem;
  }

  .stats-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }

  .stat-item {
    padding: 2rem;
  }

  .stat-item h3 {
    font-size: 2rem;
  }

  .cta-buttons {
    flex-direction: column;
    gap: 1rem;
  }

  .cta-button {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .stats-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .stat-item {
    padding: 1.5rem;
  }

  .stat-item h3 {
    font-size: 1.8rem;
  }

  .program-features li {
    font-size: 0.9rem;
    padding-left: 1rem;
  }

  .image-placeholder {
    height: 200px;
  }

  .image-placeholder span {
    font-size: 2rem;
  }

  .image-placeholder p {
    font-size: 0.9rem;
  }
}

/* Accessibility */
@media (prefers-reduced-motion: reduce) {
  .program-image img,
  .cta-button {
    transition: none;
  }
}

@media (prefers-contrast: high) {
  .program-text h3 {
    color: #000;
  }

  .program-text p {
    color: #333;
  }
} 